import { ISeriesPrimitivePaneView, SeriesPrimitivePaneViewZOrder } from 'lightweight-charts';
import type { ISeriesPrimitive, SeriesAttachedParameter } from 'lightweight-charts';

interface WatermarkOptions {
  timeframe: string;
  opacity?: number;
  fontSize?: number;
}

class WatermarkPaneView implements ISeriesPrimitivePaneView {
  private _primitive: TimeframeWatermarkPrimitive;

  constructor(primitive: TimeframeWatermarkPrimitive) {
    this._primitive = primitive;
  }

  zOrder(): SeriesPrimitivePaneViewZOrder {
    return 'bottom';
  }

  renderer() {
    return {
      draw: (target: any) => {
        const ctx = target._context;
        if (!ctx) {
          return;
        }

        const { timeframe, opacity, fontSize } = this._primitive.getData();

        ctx.save();

        // Calculate center position
        const canvasWidth = target._mediaSize.width;
        const canvasHeight = target._mediaSize.height;
        const centerX = canvasWidth / 2;
        const centerY = canvasHeight / 2;

        // Set font and measure text
        ctx.font = `700 ${fontSize}px system-ui, -apple-system, sans-serif`;
        ctx.textAlign = 'center';
        ctx.textBaseline = 'middle';

        // Draw text
        ctx.fillStyle = `rgba(255, 255, 255, ${opacity})`;
        ctx.letterSpacing = '0.1em';
        ctx.fillText(timeframe.toUpperCase(), centerX, centerY);

        ctx.restore();
      }
    };
  }
}

export class TimeframeWatermarkPrimitive implements ISeriesPrimitive<any> {
  private _paneViews: WatermarkPaneView[];
  private _timeframe: string;
  private _opacity: number;
  private _fontSize: number;
  private _requestUpdate: (() => void) | null = null;

  constructor(options: WatermarkOptions) {
    this._timeframe = options.timeframe;
    this._opacity = options.opacity || 0.2;
    this._fontSize = options.fontSize || 72;
    this._paneViews = [new WatermarkPaneView(this)];
  }

  updateTimeframe(timeframe: string) {
    this._timeframe = timeframe;
    this._requestUpdate?.();
  }

  attached(param: SeriesAttachedParameter<any>): void {
    this._requestUpdate = param.requestUpdate;
  }

  detached(): void {
    // Cleanup if needed
  }

  paneViews() {
    return this._paneViews;
  }

  updateAllViews() {
    // Views are updated via requestUpdate callback
  }

  getData() {
    return {
      timeframe: this._timeframe,
      opacity: this._opacity,
      fontSize: this._fontSize,
    };
  }
}