export enum LogLevel {
  ERROR = 0,
  WARN = 1,
  INFO = 2,
  DEBUG = 3,
  TRACE = 4
}

export interface LoggerConfig {
  level: LogLevel;
  prefix?: string;
  enableTimestamp?: boolean;
  enableColors?: boolean;
  customHandler?: (level: LogLevel, message: string, ...args: unknown[]) => void;
}

class Logger {
  private config: LoggerConfig = {
    level: LogLevel.ERROR, // Only show errors, suppress INFO/DEBUG/TRACE
    enableTimestamp: true,
    enableColors: true
  };

  private colors = {
    [LogLevel.ERROR]: '\x1b[31m', // Red
    [LogLevel.WARN]: '\x1b[33m',  // Yellow
    [LogLevel.INFO]: '\x1b[36m',  // Cyan
    [LogLevel.DEBUG]: '\x1b[90m', // Gray
    [LogLevel.TRACE]: '\x1b[37m', // White
    reset: '\x1b[0m'
  };

  configure(config: Partial<LoggerConfig>) {
    this.config = { ...this.config, ...config };
  }

  private shouldLog(level: LogLevel): boolean {
    return level <= this.config.level;
  }

  private formatMessage(level: LogLevel, message: string): string {
    const parts: string[] = [];
    
    if (this.config.enableTimestamp) {
      parts.push(`[${new Date().toISOString()}]`);
    }
    
    if (this.config.prefix) {
      parts.push(`[${this.config.prefix}]`);
    }
    
    parts.push(`[${LogLevel[level]}]`);
    parts.push(message);
    
    return parts.join(' ');
  }

  private log(level: LogLevel, message: string, ...args: unknown[]) {
    if (!this.shouldLog(level)) return;

    if (this.config.customHandler) {
      this.config.customHandler(level, message, ...args);
      return;
    }

    const formattedMessage = this.formatMessage(level, message);
    const logMethod = level === LogLevel.ERROR ? console.error : 
                     level === LogLevel.WARN ? console.warn : 
                     console.log;

    if (this.config.enableColors && typeof window === 'undefined') {
      // Node.js environment
      const color = this.colors[level];
      logMethod(`${color}${formattedMessage}${this.colors.reset}`, ...args);
    } else {
      logMethod(formattedMessage, ...args);
    }
  }

  error(message: string, ...args: unknown[]) {
    this.log(LogLevel.ERROR, message, ...args);
  }

  warn(message: string, ...args: unknown[]) {
    this.log(LogLevel.WARN, message, ...args);
  }

  info(message: string, ...args: unknown[]) {
    this.log(LogLevel.INFO, message, ...args);
  }

  debug(message: string, ...args: unknown[]) {
    this.log(LogLevel.DEBUG, message, ...args);
  }

  trace(message: string, ...args: unknown[]) {
    this.log(LogLevel.TRACE, message, ...args);
  }

  // Create a child logger with a specific prefix
  child(prefix: string): Logger {
    const childLogger = new Logger();
    childLogger.configure({
      ...this.config,
      prefix: this.config.prefix ? `${this.config.prefix}:${prefix}` : prefix
    });
    return childLogger;
  }
}

// Create default logger instance
export const logger = new Logger();

// Configure logger based on environment
if (typeof window !== 'undefined') {
  // Browser environment
  const logLevel = window.__CHART_LOG_LEVEL__ ? 
                   (LogLevel[window.__CHART_LOG_LEVEL__ as keyof typeof LogLevel] || LogLevel.INFO) :
                   (process.env.NODE_ENV === 'development' ? LogLevel.INFO : LogLevel.WARN);
  logger.configure({ level: logLevel });
} else {
  // Node.js environment
  const logLevel = process.env.LOG_LEVEL ? 
                   LogLevel[process.env.LOG_LEVEL as keyof typeof LogLevel] || LogLevel.INFO : 
                   LogLevel.INFO;
  logger.configure({ level: logLevel });
}

// Export convenience functions
export const createLogger = (prefix: string) => logger.child(prefix);