import React, { useEffect, useRef, ReactNode } from 'react';
import { useReconnection } from '../hooks/useReconnection';
import { StreamStatus } from './StreamStatus';
import { ConnectionStatus } from '../services/ReconnectionManager';
import { invoke, listen } from '../services/tauriWrapper';
import { createLogger } from '../utils/logger';

const logger = createLogger('StreamingChartWrapper');

interface StreamingChartWrapperProps {
  children: ReactNode;
  symbol: string;
  timeframe: string;
  enabled?: boolean;
  showStatus?: boolean;
  statusPosition?: 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right';
  onConnectionChange?: (status: ConnectionStatus) => void;
}

interface StreamStatus {
  connected: boolean;
  message?: string;
}

export const StreamingChartWrapper: React.FC<StreamingChartWrapperProps> = ({
  children,
  symbol,
  timeframe,
  enabled = true,
  showStatus = true,
  statusPosition = 'top-right',
  onConnectionChange,
}) => {
  const unlistenStatusRef = useRef<(() => void) | undefined>();
  const unlistenCandleRef = useRef<(() => void) | undefined>();
  const currentTimeframeRef = useRef(timeframe);

  // Connection function for the candle monitor
  const connectCandleMonitor = async () => {
    try {
      // Start the candle update monitor
      logger.debug('Starting candle update monitor...');
      await invoke('start_candle_monitor', { symbol, timeframe });
      logger.info('Candle monitor started successfully');

      // Listen for candle update events
      const updateListener = async () => {
        if (unlistenCandleRef.current) {
          unlistenCandleRef.current();
        }

        const eventName = `market-candles-updated-${currentTimeframeRef.current}`;
        logger.debug(`Listening for ${eventName} events`);

        unlistenCandleRef.current = await listen<{ symbol: string; timeframe: string; timestamp: string }>(
          eventName,
          (event) => {
            logger.debug('Candle update received:', event.payload);
            // Handle candle updates here if needed
          }
        );
      };

      await updateListener();

      // Listen for connection status
      unlistenStatusRef.current = await listen<StreamStatus>('market-stream-status', (event) => {
        logger.debug('Stream status:', event.payload);
      });
    } catch (error) {
      logger.error('Failed to start market stream:', error);
      throw error;
    }
  };

  // Use reconnection hook
  const {
    status: connectionStatus,
    retryCount,
    nextRetryDelay,
    connect: reconnect,
  } = useReconnection(connectCandleMonitor, {
    enabled: enabled && !!symbol,
    autoConnect: enabled && !!symbol,
    maxRetries: 10,
    initialDelay: 1000,
    maxDelay: 30000,
    backoffMultiplier: 1.5,
    onStatusChange: (status) => {
      logger.info('Connection status changed:', status);
      onConnectionChange?.(status);
    },
    onError: (error, retryCount) => {
      logger.error(`Connection error (retry ${retryCount}):`, error);
    },
    onMaxRetriesReached: () => {
      logger.error('Failed to connect after maximum retries');
    },
  });

  // Update timeframe listener when timeframe changes
  useEffect(() => {
    currentTimeframeRef.current = timeframe;
    if (connectionStatus === 'connected' && unlistenCandleRef.current) {
      const updateListener = async () => {
        if (unlistenCandleRef.current) {
          unlistenCandleRef.current();
        }

        const eventName = `market-candles-updated-${timeframe}`;
        logger.debug(`Updating listener to ${eventName}`);

        try {
          unlistenCandleRef.current = await listen<{ symbol: string; timeframe: string; timestamp: string }>(
            eventName,
            (event) => {
              logger.debug('Candle update received:', event.payload);
            }
          );
        } catch (error) {
          logger.error('Failed to update candle listener:', error);
        }
      };

      updateListener();
    }
  }, [timeframe, connectionStatus]);

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      logger.debug('Cleaning up streaming wrapper');
      if (unlistenStatusRef.current) unlistenStatusRef.current();
      if (unlistenCandleRef.current) unlistenCandleRef.current();
      
      invoke('stop_candle_monitor').catch((error) => {
        logger.error('Failed to stop candle monitor:', error);
      });
    };
  }, []);

  // Determine connection message
  const connectionMessage = connectionStatus === 'reconnecting' && retryCount > 0
    ? `Retry ${retryCount} in ${Math.ceil(nextRetryDelay / 1000)}s`
    : undefined;

  return (
    <div style={{ position: 'relative', width: '100%', height: '100%' }}>
      {children}
      
      {enabled && showStatus && (
        <StreamStatus
          status={connectionStatus}
          symbol={symbol}
          message={connectionMessage}
          onRetry={reconnect}
          position={statusPosition}
          compact={false}
        />
      )}
    </div>
  );
};