import React from 'react';

interface LoadingStateProps {
  message?: string;
  subMessage?: string;
  showSpinner?: boolean;
  height?: string | number;
  overlay?: boolean;
}

export const LoadingState: React.FC<LoadingStateProps> = ({
  message = 'Loading...',
  subMessage,
  showSpinner = true,
  height = '100%',
  overlay = false,
}) => {
  const content = (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        height: typeof height === 'number' ? `${height}px` : height,
        minHeight: overlay ? 'auto' : '200px',
        padding: '40px',
        backgroundColor: overlay ? 'rgba(255, 255, 255, 0.9)' : 'transparent',
        position: overlay ? 'absolute' : 'relative',
        inset: overlay ? 0 : 'auto',
        zIndex: overlay ? 1000 : 'auto',
      }}
    >
      {showSpinner && (
        <div
          style={{
            width: '40px',
            height: '40px',
            marginBottom: '20px',
            border: '3px solid #e9ecef',
            borderTopColor: '#007bff',
            borderRadius: '50%',
            animation: 'spin 1s linear infinite',
          }}
        />
      )}
      <div
        style={{
          fontSize: '16px',
          fontWeight: '500',
          color: '#495057',
          marginBottom: subMessage ? '8px' : 0,
        }}
      >
        {message}
      </div>
      {subMessage && (
        <div
          style={{
            fontSize: '14px',
            color: '#6c757d',
          }}
        >
          {subMessage}
        </div>
      )}
      <style>
        {`
          @keyframes spin {
            0% { transform: rotate(0deg); }
            100% { transform: rotate(360deg); }
          }
        `}
      </style>
    </div>
  );

  return content;
};

// Chart-specific loading state
export const ChartLoadingState: React.FC<{ symbol?: string; timeframe?: string }> = ({
  symbol,
  timeframe,
}) => {
  return (
    <LoadingState
      message={`Loading ${symbol ? `${symbol} ` : ''}chart data${timeframe ? ` (${timeframe})` : ''}...`}
      height="400px"
    />
  );
};

// Skeleton loader for chart area
export const ChartSkeleton: React.FC<{ height?: string | number }> = ({ height = '400px' }) => {
  return (
    <div
      style={{
        position: 'relative',
        width: '100%',
        height: typeof height === 'number' ? `${height}px` : height,
        backgroundColor: '#f8f9fa',
        borderRadius: '8px',
        overflow: 'hidden',
      }}
    >
      {/* Price axis skeleton */}
      <div
        style={{
          position: 'absolute',
          right: 0,
          top: 0,
          bottom: 40,
          width: '60px',
          backgroundColor: '#e9ecef',
          animation: 'shimmer 1.5s infinite',
        }}
      />
      
      {/* Time axis skeleton */}
      <div
        style={{
          position: 'absolute',
          left: 0,
          right: 60,
          bottom: 0,
          height: '40px',
          backgroundColor: '#e9ecef',
          animation: 'shimmer 1.5s infinite',
          animationDelay: '0.2s',
        }}
      />
      
      {/* Chart area skeleton */}
      <div
        style={{
          position: 'absolute',
          left: 0,
          right: 60,
          top: 0,
          bottom: 40,
          padding: '20px',
        }}
      >
        {/* Candle placeholders */}
        <div style={{ display: 'flex', justifyContent: 'space-around', height: '100%', alignItems: 'flex-end' }}>
          {[...Array(8)].map((_, i) => (
            <div
              key={i}
              style={{
                width: '8px',
                height: `${Math.random() * 60 + 20}%`,
                backgroundColor: '#dee2e6',
                borderRadius: '2px',
                animation: 'shimmer 1.5s infinite',
                animationDelay: `${i * 0.1}s`,
              }}
            />
          ))}
        </div>
      </div>
      
      <style>
        {`
          @keyframes shimmer {
            0% { opacity: 0.6; }
            50% { opacity: 0.8; }
            100% { opacity: 0.6; }
          }
        `}
      </style>
    </div>
  );
};

// Inline loading indicator for updates
export const InlineLoader: React.FC<{ text?: string }> = ({ text = 'Updating...' }) => {
  return (
    <div
      style={{
        display: 'inline-flex',
        alignItems: 'center',
        gap: '8px',
        padding: '4px 12px',
        backgroundColor: '#e7f3ff',
        borderRadius: '4px',
        fontSize: '13px',
        color: '#0066cc',
      }}
    >
      <div
        style={{
          width: '12px',
          height: '12px',
          border: '2px solid #cce5ff',
          borderTopColor: '#0066cc',
          borderRadius: '50%',
          animation: 'spin 0.8s linear infinite',
        }}
      />
      {text}
      <style>
        {`
          @keyframes spin {
            0% { transform: rotate(0deg); }
            100% { transform: rotate(360deg); }
          }
        `}
      </style>
    </div>
  );
};