import { useEffect, useRef, useState, useCallback } from 'react';
import { ReconnectionManager, WebSocketReconnectionManager, ConnectionStatus, ReconnectionConfig } from '../services/ReconnectionManager';
import { createLogger } from '../utils/logger';
import { ChartData } from '../types';

const logger = createLogger('useReconnection');

export interface UseReconnectionOptions extends ReconnectionConfig {
  enabled?: boolean;
  autoConnect?: boolean;
}

export interface UseReconnectionReturn {
  status: ConnectionStatus;
  retryCount: number;
  nextRetryDelay: number;
  connect: () => Promise<void>;
  disconnect: (reason?: string) => void;
  resetRetries: () => void;
}

export function useReconnection(
  connectFn: () => Promise<void>,
  options: UseReconnectionOptions = {}
): UseReconnectionReturn {
  const { enabled = true, autoConnect = true, ...config } = options;
  const [status, setStatus] = useState<ConnectionStatus>('disconnected');
  const [retryCount, setRetryCount] = useState(0);
  const [nextRetryDelay, setNextRetryDelay] = useState(0);
  
  const managerRef = useRef<ReconnectionManager>();

  useEffect(() => {
    if (!enabled) return;

    const manager = new ReconnectionManager(connectFn, {
      ...config,
      onStatusChange: (newStatus) => {
        setStatus(newStatus);
        setRetryCount(manager.getRetryCount());
        setNextRetryDelay(manager.getNextRetryDelay());
        config.onStatusChange?.(newStatus);
      },
    });

    managerRef.current = manager;

    if (autoConnect) {
      manager.connect();
    }

    return () => {
      manager.destroy();
    };
  }, [enabled]); // eslint-disable-line react-hooks/exhaustive-deps

  const connect = useCallback(async () => {
    await managerRef.current?.connect();
  }, []);

  const disconnect = useCallback((reason?: string) => {
    managerRef.current?.disconnect(reason);
  }, []);

  const resetRetries = useCallback(() => {
    managerRef.current?.resetRetryCount();
    setRetryCount(0);
    setNextRetryDelay(config.initialDelay || 1000);
  }, [config.initialDelay]);

  return {
    status,
    retryCount,
    nextRetryDelay,
    connect,
    disconnect,
    resetRetries,
  };
}

// WebSocket-specific hook
export interface UseWebSocketReconnectionOptions extends Omit<UseReconnectionOptions, 'onError'> {
  url: string;
  protocols?: string | string[];
  enableHeartbeat?: boolean;
  pingInterval?: number;
  pongTimeout?: number;
  onMessage?: (event: MessageEvent) => void;
  onOpen?: (event: Event) => void;
  onClose?: (event: CloseEvent) => void;
  onError?: (error: Error, retryCount: number) => void;
  onWebSocketError?: (event: Event) => void;
}

export interface UseWebSocketReconnectionReturn extends UseReconnectionReturn {
  ws: WebSocket | undefined;
  send: (data: string | ArrayBuffer | Blob) => void;
  close: (code?: number, reason?: string) => void;
}

export function useWebSocketReconnection(
  options: UseWebSocketReconnectionOptions
): UseWebSocketReconnectionReturn {
  const { 
    url, 
    protocols, 
    enableHeartbeat = true,
    pingInterval,
    pongTimeout,
    onMessage,
    onOpen,
    onClose,
    onError,
    onWebSocketError,
    enabled = true,
    autoConnect = true,
    ...reconnectionConfig 
  } = options;

  const [ws, setWs] = useState<WebSocket>();
  const managerRef = useRef<WebSocketReconnectionManager>();

  useEffect(() => {
    if (!enabled) return;

    const manager = new WebSocketReconnectionManager(url, protocols, {
      ...reconnectionConfig,
      onError,
      enableHeartbeat,
      pingInterval,
      pongTimeout,
      onMessage,
      onConnected: () => {
        const websocket = manager.getWebSocket();
        setWs(websocket);
        
        if (websocket && onOpen) {
          onOpen(new Event('open'));
        }
        
        reconnectionConfig.onConnected?.();
      },
      onDisconnected: (reason) => {
        setWs(undefined);
        
        if (onClose) {
          onClose(new CloseEvent('close', { reason }));
        }
        
        reconnectionConfig.onDisconnected?.(reason);
      },
    });

    managerRef.current = manager;

    if (autoConnect) {
      manager.connect();
    }

    return () => {
      manager.destroy();
    };
  }, [url, enabled]); // eslint-disable-line react-hooks/exhaustive-deps

  const reconnectionReturn = useReconnection(
    async () => {
      // This is handled by the WebSocketReconnectionManager
      await Promise.resolve();
    },
    { ...reconnectionConfig, enabled: false } // Disable the base hook
  );

  const send = useCallback((data: string | ArrayBuffer | Blob) => {
    managerRef.current?.send(data);
  }, []);

  const close = useCallback((code?: number, reason?: string) => {
    managerRef.current?.close(code, reason);
  }, []);

  return {
    ...reconnectionReturn,
    status: managerRef.current?.getStatus() || 'disconnected',
    retryCount: managerRef.current?.getRetryCount() || 0,
    nextRetryDelay: managerRef.current?.getNextRetryDelay() || 0,
    ws,
    send,
    close,
    connect: async () => {
      await managerRef.current?.connect();
    },
    disconnect: (reason?: string) => {
      managerRef.current?.disconnect(reason);
    },
  };
}

// Example usage helper for chart data streaming
export function useChartDataStream(
  symbol: string,
  timeframe: string,
  onCandle: (candle: ChartData) => void
) {
  const wsUrl = `wss://stream.example.com/ws/${symbol.toLowerCase()}@kline_${timeframe}`;
  
  const { status, send, retryCount } = useWebSocketReconnection({
    url: wsUrl,
    enableHeartbeat: true,
    maxRetries: 10,
    initialDelay: 1000,
    maxDelay: 30000,
    onMessage: (event) => {
      try {
        const data = JSON.parse(event.data);
        if (data.type === 'kline') {
          onCandle(data.kline);
        }
      } catch (error) {
        logger.error('Failed to parse WebSocket message:', error);
      }
    },
    onConnected: () => {
      logger.info(`Connected to market data stream for ${symbol}`);
      // Subscribe to the stream
      send(JSON.stringify({
        method: 'SUBSCRIBE',
        params: [`${symbol.toLowerCase()}@kline_${timeframe}`],
        id: Date.now(),
      }));
    },
    onError: (error, retryCount) => {
      logger.error(`Stream error (retry ${retryCount}):`, error);
    },
    onMaxRetriesReached: () => {
      logger.error('Failed to connect to market data stream after max retries');
    },
  });

  return { status, retryCount };
}