import React, { useEffect, useState } from 'react';
import { Timeframe } from '../constants/timeframes';

interface TimeframeWatermarkProps {
  timeframe: string;
  position?: 'center' | 'bottom-right' | 'bottom-left' | 'top-right' | 'axis-center' | 'axis-right' | 'custom';
  customPosition?: React.CSSProperties; // For custom positioning
  opacity?: number;
  fontSize?: string;
  color?: string;
}

export const TimeframeWatermark: React.FC<TimeframeWatermarkProps> = ({
  timeframe,
  position = 'center',
  customPosition,
  opacity = 0.08,
  fontSize = 'min(10vw, 72px)',
  color = 'rgba(255, 255, 255, opacity)'
}) => {
  const [isAnimating, setIsAnimating] = useState(false);

  // Trigger animation when timeframe changes
  useEffect(() => {
    // Always animate on timeframe change, regardless of animate prop
    setIsAnimating(true);
    const timer = setTimeout(() => setIsAnimating(false), 300);
    return () => clearTimeout(timer);
  }, [timeframe]); // Only depend on timeframe, not animate prop

  // Position styles based on position prop
  const positionStyles = {
    center: {
      bottom: 'calc(20px + 6vh)',
      left: '50%',
      transform: 'translateX(-50%)',
    },
    'bottom-right': {
      bottom: '20px',
      right: '20px',
    },
    'bottom-left': {
      bottom: '20px',
      left: '20px',
    },
    'top-right': {
      top: '60px', // Below other UI elements
      right: '20px',
    },
    'axis-center': {
      bottom: '5px', // Inside the time axis area
      left: '50%',
      transform: 'translateX(-50%)',
    },
    'axis-right': {
      bottom: '5px', // Inside the time axis area
      right: '65px', // Account for price axis width
    },
    custom: customPosition || {},
  };

  // Validate timeframe
  const displayTimeframe = (timeframe as Timeframe) || timeframe;

  const baseTransform = 'transform' in positionStyles[position]
    ? (positionStyles[position] as any).transform
    : '';

  return (
    <div
      style={{
        position: 'absolute',
        ...positionStyles[position],
        fontSize,
        fontWeight: '700',
        color: color.replace('opacity', opacity.toString()),
        pointerEvents: 'none',
        userSelect: 'none',
        fontFamily: 'system-ui, -apple-system, sans-serif',
        letterSpacing: '0.1em',
        textTransform: 'uppercase',
        transition: isAnimating
          ? 'all 300ms cubic-bezier(0.4, 0.0, 0.2, 1)'
          : 'opacity 200ms ease-out',
        opacity: isAnimating ? Math.min(opacity * 1.8, 0.35) : opacity, // Subtle flash: 1.8x brighter
        transform: isAnimating
          ? `${baseTransform} scale(1.015)`.trim()
          : baseTransform || 'none',
        textShadow: isAnimating
          ? `0 0 10px rgba(255, 255, 255, ${opacity * 1.2})`
          : 'none',
        filter: isAnimating ? 'brightness(1.1)' : 'none',
        zIndex: 1000,
      }}
    >
      {displayTimeframe}
    </div>
  );
};

// Minimal version for subtle display
export const MinimalTimeframeWatermark: React.FC<{ timeframe: string }> = ({ timeframe }) => (
  <TimeframeWatermark 
    timeframe={timeframe} 
    opacity={0.05} 
    fontSize="min(8vw, 64px)"
  />
);