import React from 'react';
import { createLogger } from '../utils/logger';
import { ConnectionStatus } from '../services/ReconnectionManager';

const logger = createLogger('StreamStatus');

interface StreamStatusProps {
  status: ConnectionStatus;
  symbol?: string;
  message?: string;
  onRetry?: () => void;
  position?: 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right';
  compact?: boolean;
}

interface StatusConfig {
  color: string;
  bgColor: string;
  icon: string;
  label: string;
  animate?: boolean;
}

const statusConfig: Record<ConnectionStatus, StatusConfig> = {
  connected: {
    color: '#4caf50',
    bgColor: 'rgba(76, 175, 80, 0.1)',
    icon: 'â—',
    label: 'Connected',
  },
  connecting: {
    color: '#2196f3',
    bgColor: 'rgba(33, 150, 243, 0.1)',
    icon: 'â—',
    label: 'Connecting',
    animate: true,
  },
  disconnected: {
    color: '#757575',
    bgColor: 'rgba(117, 117, 117, 0.1)',
    icon: 'â—‹',
    label: 'Disconnected',
  },
  error: {
    color: '#f44336',
    bgColor: 'rgba(244, 67, 54, 0.1)',
    icon: 'âœ•',
    label: 'Error',
  },
  reconnecting: {
    color: '#ff9800',
    bgColor: 'rgba(255, 152, 0, 0.1)',
    icon: 'â†»',
    label: 'Reconnecting',
    animate: true,
  },
};

export const StreamStatus: React.FC<StreamStatusProps> = ({
  status,
  symbol,
  message,
  onRetry,
  position = 'top-right',
  compact = false,
}) => {
  const config = statusConfig[status];
  
  React.useEffect(() => {
    logger.debug(`Status changed to: ${status}`, { symbol, message });
  }, [status, symbol, message]);

  const positionStyles = {
    'top-left': { top: 10, left: 10 },
    'top-right': { top: 10, right: 10 },
    'bottom-left': { bottom: 10, left: 10 },
    'bottom-right': { bottom: 10, right: 10 },
  };

  const handleRetry = () => {
    logger.info('Retry connection requested');
    onRetry?.();
  };

  return (
    <div
      style={{
        position: 'absolute',
        ...positionStyles[position],
        padding: compact ? '4px 8px' : '8px 12px',
        borderRadius: '4px',
        backgroundColor: config.bgColor,
        border: `1px solid ${config.color}`,
        display: 'flex',
        alignItems: 'center',
        gap: '8px',
        fontSize: compact ? '12px' : '14px',
        fontFamily: 'system-ui, -apple-system, sans-serif',
        zIndex: 1000,
        userSelect: 'none',
        transition: 'all 0.3s ease',
      }}
    >
      <span
        style={{
          color: config.color,
          fontSize: compact ? '12px' : '16px',
          animation: config.animate ? 'pulse 1.5s infinite' : 'none',
        }}
      >
        {config.icon}
      </span>
      
      {!compact && (
        <>
          <span style={{ color: config.color, fontWeight: 500 }}>
            {config.label}
          </span>
          
          {symbol && (
            <span style={{ color: config.color, opacity: 0.8 }}>
              {symbol}
            </span>
          )}
          
          {message && (
            <span style={{ color: config.color, opacity: 0.7, fontSize: '12px' }}>
              {message}
            </span>
          )}
          
          {(status === 'error' || status === 'disconnected') && onRetry && (
            <button
              onClick={handleRetry}
              style={{
                marginLeft: '8px',
                padding: '2px 8px',
                fontSize: '12px',
                color: config.color,
                backgroundColor: 'transparent',
                border: `1px solid ${config.color}`,
                borderRadius: '3px',
                cursor: 'pointer',
                transition: 'all 0.2s ease',
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.backgroundColor = config.color;
                e.currentTarget.style.color = '#ffffff';
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.backgroundColor = 'transparent';
                e.currentTarget.style.color = config.color;
              }}
            >
              Retry
            </button>
          )}
        </>
      )}
      
      <style>
        {`
          @keyframes pulse {
            0% { opacity: 1; }
            50% { opacity: 0.5; }
            100% { opacity: 1; }
          }
        `}
      </style>
    </div>
  );
};

// Minimal version for space-constrained layouts
export const MinimalStreamStatus: React.FC<Pick<StreamStatusProps, 'status'>> = ({ status }) => {
  const config = statusConfig[status];
  
  return (
    <span
      style={{
        color: config.color,
        fontSize: '14px',
        animation: config.animate ? 'pulse 1.5s infinite' : 'none',
      }}
      title={config.label}
    >
      {config.icon}
    </span>
  );
};