import { WebSocketReconnectionManager } from './ReconnectionManager';
import { createLogger } from '../utils/logger';
import { ChartData } from '../types';

const logger = createLogger('WebSocketService');

export interface WebSocketConfig {
  url: string;
  apiKey?: string;
  broker?: string; // Add broker field
  enableHeartbeat?: boolean;
  pingInterval?: number;
  pongTimeout?: number;
  maxRetries?: number;
  initialDelay?: number;
  maxDelay?: number;
}

export interface StreamMessage {
  type: 'candle' | 'trade' | 'error' | 'subscribed' | 'unsubscribed';
  symbol?: string;
  timeframe?: string;
  data?: any;
  error?: string;
}

export interface CandleUpdate {
  time: number;
  open: number;
  high: number;
  low: number;
  close: number;
  volume?: number;
}

export interface Subscription {
  symbol: string;
  timeframe: string;
  callback: (candle: ChartData) => void;
  onRawMessage?: (message: any) => void; // Add raw message callback
}

export class WebSocketService {
  private manager?: WebSocketReconnectionManager;
  private subscriptions = new Map<string, Subscription>();
  private config: WebSocketConfig;
  private isConnected = false;

  constructor(config: WebSocketConfig) {
    this.config = config;
  }

  connect(): Promise<void> {
    return new Promise((resolve, reject) => {
      logger.info('Connecting to WebSocket server...', { url: this.config.url });

      this.manager = new WebSocketReconnectionManager(
        this.config.url,
        undefined,
        {
          enableHeartbeat: this.config.enableHeartbeat ?? true,
          pingInterval: this.config.pingInterval ?? 30000,
          pongTimeout: this.config.pongTimeout ?? 5000,
          maxRetries: this.config.maxRetries ?? 10,
          initialDelay: this.config.initialDelay ?? 1000,
          maxDelay: this.config.maxDelay ?? 30000,
          onConnected: () => {
            logger.info('WebSocket connected');
            this.isConnected = true;
            this.sendAuthMessage();
            this.resubscribeAll();
            resolve();
          },
          onDisconnected: (reason) => {
            logger.warn('WebSocket disconnected', { reason });
            this.isConnected = false;
          },
          onError: (error, retryCount) => {
            logger.error('WebSocket error', { error, retryCount });
            if (retryCount === 0) {
              reject(error);
            }
          },
          onMessage: (event) => {
            this.handleMessage(event);
          }
        }
      );

      this.manager.connect();
    });
  }

  private sendAuthMessage() {
    if (this.config.apiKey) {
      this.send({
        type: 'auth',
        apiKey: this.config.apiKey
      });
    }
  }

  private resubscribeAll() {
    logger.info('Resubscribing to all active subscriptions', { 
      count: this.subscriptions.size 
    });
    
    this.subscriptions.forEach(sub => {
      this.sendSubscribeMessage(sub.symbol, sub.timeframe);
    });
  }

  private handleMessage(event: MessageEvent) {
    try {
      // Skip ping/pong heartbeat messages (not JSON)
      if (event.data === 'ping' || event.data === 'pong') {
        return;
      }

      const message = JSON.parse(event.data);
      logger.info('Received WebSocket message', { message });
      
      // Send raw message to all subscribers
      this.subscriptions.forEach(sub => {
        if (sub.onRawMessage) {
          sub.onRawMessage(message);
        }
      });

      // Handle your server's format
      if (message.type === 'data' && message.data) {
        const innerData = message.data;
        
        // Convert server format to our expected format
        if (innerData.type === 'candle' && innerData.data) {
          const candleMessage: StreamMessage = {
            type: 'candle',
            symbol: message.symbol,
            timeframe: innerData.interval || '1h',
            data: {
              time: innerData.timestamp || innerData.data.time,
              open: innerData.data.open,
              high: innerData.data.high,
              low: innerData.data.low,
              close: innerData.data.close,
              volume: innerData.data.volume
            }
          };
          this.handleCandleUpdate(candleMessage);
        } else if (innerData.type === 'tick' && innerData.data) {
          // For tick data, create a candle (you might want to aggregate these)
          const candleMessage: StreamMessage = {
            type: 'candle',
            symbol: message.symbol,
            timeframe: '1m', // Default timeframe for ticks
            data: {
              time: innerData.timestamp,
              open: innerData.data.price,
              high: innerData.data.price,
              low: innerData.data.price,
              close: innerData.data.price,
              volume: innerData.data.volume
            }
          };
          this.handleCandleUpdate(candleMessage);
        }
      } else if (message.type === 'status' || message.type === 'subscribed') {
        logger.info('Status update', { message });
      } else if (message.type === 'error') {
        logger.error('Stream error', { error: message.error || message });
      }
    } catch (error) {
      logger.error('Failed to parse WebSocket message', { error, data: event.data });
    }
  }

  private handleCandleUpdate(message: StreamMessage) {
    if (!message.symbol || !message.timeframe || !message.data) {
      logger.warn('Invalid candle update message', { message });
      return;
    }

    const key = this.getSubscriptionKey(message.symbol, message.timeframe);
    const subscription = this.subscriptions.get(key);
    
    if (subscription) {
      const candle: ChartData = {
        time: message.data.time,
        open: message.data.open,
        high: message.data.high,
        low: message.data.low,
        close: message.data.close
      };
      
      subscription.callback(candle);
    } else {
      logger.trace('Received update for unsubscribed symbol', { 
        symbol: message.symbol, 
        timeframe: message.timeframe 
      });
    }
  }

  subscribe(
    symbol: string, 
    timeframe: string, 
    callback: (candle: ChartData) => void,
    onRawMessage?: (message: any) => void
  ): () => void {
    const key = this.getSubscriptionKey(symbol, timeframe);
    
    if (this.subscriptions.has(key)) {
      logger.warn('Already subscribed', { symbol, timeframe });
      return () => {};
    }

    logger.info('Subscribing to stream', { symbol, timeframe });
    
    const subscription: Subscription = { symbol, timeframe, callback, onRawMessage };
    this.subscriptions.set(key, subscription);

    if (this.isConnected) {
      this.sendSubscribeMessage(symbol, timeframe);
    }

    // Return unsubscribe function
    return () => {
      this.unsubscribe(symbol, timeframe);
    };
  }

  unsubscribe(symbol: string, timeframe: string) {
    const key = this.getSubscriptionKey(symbol, timeframe);
    
    if (!this.subscriptions.has(key)) {
      logger.warn('Not subscribed', { symbol, timeframe });
      return;
    }

    logger.info('Unsubscribing from stream', { symbol, timeframe });
    
    this.subscriptions.delete(key);

    if (this.isConnected) {
      this.sendUnsubscribeMessage(symbol, timeframe);
    }
  }

  private sendSubscribeMessage(symbol: string, timeframe: string) {
    // Send in your server's expected format
    const broker = this.config.broker || 'binance';
    this.send({
      action: 'subscribe',
      broker: broker,
      symbols: [symbol],
      types: broker === 'oanda' || broker === 'kraken' ? ['tick'] : ['candle'], // Oanda and Kraken use tick data
      interval: timeframe
    });
  }

  private sendUnsubscribeMessage(symbol: string, _timeframe: string) {
    this.send({
      action: 'unsubscribe',
      symbols: [symbol]
    });
  }

  private send(data: any) {
    if (!this.manager) {
      logger.error('Cannot send: WebSocket manager not initialized');
      return;
    }

    const message = JSON.stringify(data);
    logger.trace('Sending message', { data });
    this.manager.send(message);
  }

  private getSubscriptionKey(symbol: string, timeframe: string): string {
    return `${symbol}_${timeframe}`;
  }

  disconnect() {
    logger.info('Disconnecting WebSocket');
    this.subscriptions.clear();
    
    // Don't destroy the manager, just close the WebSocket connection
    // This allows reconnection without creating a new manager instance
    if (this.manager) {
      const ws = (this.manager as any).getWebSocket?.();
      if (ws && ws.readyState === WebSocket.OPEN) {
        ws.close(1000, 'Client disconnect');
      }
    }
    
    this.isConnected = false;
  }

  getStatus() {
    return this.manager?.getStatus() ?? 'disconnected';
  }

  getRetryCount() {
    return this.manager?.getRetryCount() ?? 0;
  }

  isConnectedStatus() {
    return this.isConnected;
  }
}

// Singleton instance
let webSocketService: WebSocketService | null = null;

export function getWebSocketService(config?: WebSocketConfig): WebSocketService {
  if (!webSocketService && config) {
    webSocketService = new WebSocketService(config);
  }
  
  if (!webSocketService) {
    throw new Error('WebSocketService not initialized. Please provide config.');
  }
  
  return webSocketService;
}

export function destroyWebSocketService() {
  if (webSocketService) {
    webSocketService.disconnect();
    // Don't null out the service - keep the singleton instance
    // This prevents creating multiple instances in React StrictMode
  }
}