import { createLogger } from '../utils/logger';
import { Candle } from '../types';

interface HTTPDataProviderConfig {
  baseUrl: string;
  apiKey?: string;
  timeout?: number;
}

interface FetchCandlesParams {
  symbol: string;
  timeframe: string;
  from: number;
  to: number;
}

export class HTTPDataProvider {
  private config: HTTPDataProviderConfig;
  private logger = createLogger('HTTPDataProvider');

  constructor(config: HTTPDataProviderConfig) {
    this.config = {
      timeout: 30000, // 30 second default timeout
      ...config
    };
  }

  /**
   * Fetch historical candles from the API
   */
  async fetchCandles(params: FetchCandlesParams): Promise<Candle[]> {
    const { symbol, timeframe, from, to } = params;

    // Build query string
    const queryParams = new URLSearchParams({
      symbol,
      timeframe,
      from: from.toString(),
      to: to.toString()
    });

    const url = `${this.config.baseUrl}/api/candles?${queryParams}`;

    const startTime = performance.now();
    this.logger.info(`ðŸ“Š Fetching candles: ${symbol} ${timeframe} [${from} - ${to}]`);
    this.logger.debug(`Full URL: ${url}`);

    try {
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), this.config.timeout);

      const headers: HeadersInit = {
        'Content-Type': 'application/json'
      };

      // Add API key if provided
      if (this.config.apiKey) {
        headers['Authorization'] = `Bearer ${this.config.apiKey}`;
      }

      const response = await fetch(url, {
        method: 'GET',
        headers,
        signal: controller.signal
      });

      clearTimeout(timeoutId);

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const data = await response.json();

      if (!Array.isArray(data)) {
        this.logger.error('Invalid response format: expected array', data);
        return [];
      }

      // Log any bad candles in the API response
      const badCandles = data.filter((c: any) =>
        c.time == null || c.open == null || c.high == null || c.low == null || c.close == null
      );
      if (badCandles.length > 0) {
        this.logger.error(`Found ${badCandles.length} bad candles in API response:`, badCandles.slice(0, 5));
      }

      // Validate and transform data - filter out candles with null OHLC values
      const candles: Candle[] = data
        .filter((candle: any) => {
          const isValid =
            candle.time != null &&
            candle.open != null && typeof candle.open === 'number' && !isNaN(candle.open) &&
            candle.high != null && typeof candle.high === 'number' && !isNaN(candle.high) &&
            candle.low != null && typeof candle.low === 'number' && !isNaN(candle.low) &&
            candle.close != null && typeof candle.close === 'number' && !isNaN(candle.close);

          return isValid;
        })
        .map((candle: any) => ({
          time: candle.time,
          open: candle.open,
          high: candle.high,
          low: candle.low,
          close: candle.close
        }));

      const elapsed = ((performance.now() - startTime) / 1000).toFixed(2);
      this.logger.info(`âœ… Fetched ${candles.length} candles for ${symbol} ${timeframe} in ${elapsed}s`);
      return candles;

    } catch (error) {
      if (error instanceof Error) {
        if (error.name === 'AbortError') {
          this.logger.error(`Request timeout after ${this.config.timeout}ms`);
          throw new Error('Request timeout');
        }
        this.logger.error('Fetch error:', error.message);
        throw error;
      }
      throw new Error('Unknown error fetching candles');
    }
  }

  /**
   * Fetch metadata for a symbol (date range info)
   */
  async fetchMetadata(symbol: string): Promise<{ earliest: number; latest: number }> {
    const url = `${this.config.baseUrl}/api/metadata?symbol=${symbol}`;

    this.logger.debug(`Fetching metadata for ${symbol}`);

    try {
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), this.config.timeout);

      const headers: HeadersInit = {
        'Content-Type': 'application/json'
      };

      if (this.config.apiKey) {
        headers['Authorization'] = `Bearer ${this.config.apiKey}`;
      }

      const response = await fetch(url, {
        method: 'GET',
        headers,
        signal: controller.signal
      });

      clearTimeout(timeoutId);

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const data = await response.json();

      if (!data.earliest || !data.latest) {
        this.logger.error('Invalid metadata format:', data);
        throw new Error('Invalid metadata response');
      }

      this.logger.info(`Metadata for ${symbol}: ${new Date(data.earliest * 1000).toISOString()} to ${new Date(data.latest * 1000).toISOString()}`);

      return {
        earliest: data.earliest,
        latest: data.latest
      };

    } catch (error) {
      if (error instanceof Error) {
        if (error.name === 'AbortError') {
          this.logger.error(`Metadata request timeout after ${this.config.timeout}ms`);
          throw new Error('Metadata request timeout');
        }
        this.logger.error('Metadata fetch error:', error.message);
        throw error;
      }
      throw new Error('Unknown error fetching metadata');
    }
  }

  /**
   * Update configuration
   */
  updateConfig(config: Partial<HTTPDataProviderConfig>) {
    this.config = { ...this.config, ...config };
    // Redact sensitive information before logging
    const safeConfig = {
      baseUrl: this.config.baseUrl,
      timeout: this.config.timeout,
      apiKey: this.config.apiKey ? '[REDACTED]' : undefined
    };
    this.logger.info('Configuration updated', safeConfig);
  }

  /**
   * Get current configuration (without sensitive data)
   */
  getConfig() {
    return {
      baseUrl: this.config.baseUrl,
      timeout: this.config.timeout,
      hasApiKey: !!this.config.apiKey
    };
  }
}

// Singleton instance
let httpDataProvider: HTTPDataProvider | null = null;

/**
 * Get or create the HTTPDataProvider singleton
 */
export function getHTTPDataProvider(config?: HTTPDataProviderConfig): HTTPDataProvider {
  if (!httpDataProvider && !config) {
    throw new Error('HTTPDataProvider must be initialized with config on first call');
  }

  if (config && !httpDataProvider) {
    httpDataProvider = new HTTPDataProvider(config);
  } else if (config && httpDataProvider) {
    httpDataProvider.updateConfig(config);
  }

  return httpDataProvider!;
}

/**
 * Destroy the singleton instance
 */
export function destroyHTTPDataProvider() {
  httpDataProvider = null;
}
