import { create } from 'zustand';
import { devtools, persist } from 'zustand/middleware';
import { createLogger } from '../utils/logger';

const logger = createLogger('useTradingStore');

interface TradingState {
  // Chart settings (from TradingContext)
  selectedPair: string;
  selectedTimeframe: string;
  chartType: 'candlestick' | 'line' | 'bar';
  chartVersion: 'v1' | 'v2';

  // Indicators
  indicators: {
    ma: boolean;
    rsi: boolean;
    macd: boolean;
    volume: boolean;
  };

  // Actions
  setPair: (pair: string) => void;
  setTimeframe: (tf: string) => void;
  setChartType: (type: 'candlestick' | 'line' | 'bar') => void;
  setChartVersion: (version: 'v1' | 'v2') => void;
  toggleIndicator: (indicator: keyof TradingState['indicators']) => void;
}

export const useTradingStore = create<TradingState>()(
  devtools(
    persist(
      (set) => ({
        // Initial state - matches TradingContext defaults
        selectedPair: 'EURUSD',
        selectedTimeframe: '1h',
        chartType: 'candlestick',
        chartVersion: 'v1',
        indicators: {
          ma: false,
          rsi: false,
          macd: false,
          volume: false,
        },

        // Actions - same API as TradingContext
        setPair: (pair) => {
          logger.debug('setPair called with:', pair);
          set((state) => {
            logger.debug('Current selectedPair:', state.selectedPair);
            logger.debug('New selectedPair will be:', pair);
            return { selectedPair: pair };
          });
        },

        setTimeframe: (timeframe) => {
          logger.debug('setTimeframe:', timeframe);
          set({ selectedTimeframe: timeframe });
        },

        setChartType: (chartType) => {
          logger.debug('setChartType:', chartType);
          set({ chartType });
        },

        setChartVersion: (chartVersion) => {
          logger.debug('setChartVersion:', chartVersion);
          set({ chartVersion });
        },

        toggleIndicator: (indicator) => {
          logger.debug('toggleIndicator:', indicator);
          set((state) => ({
            indicators: {
              ...state.indicators,
              [indicator]: !state.indicators[indicator],
            },
          }));
        },
      }),
      {
        name: 'trading-storage',
        // Only persist user preferences, not UI state
        partialize: (state) => ({
          selectedPair: state.selectedPair,
          selectedTimeframe: state.selectedTimeframe,
          chartType: state.chartType,
          chartVersion: state.chartVersion,
          indicators: state.indicators,
        }),
      }
    ),
    {
      name: 'trading-store',
    }
  )
);

// Compatibility hook to ease migration
export const useTrading = () => {
  const state = useTradingStore();
  return {
    ...state,
    // Ensure complete compatibility with TradingContext API
    setTimeframe: state.setTimeframe,
  };
};
