import React, { useState, useEffect, useRef } from 'react';
import { ChartData } from '../types';
import { MarketDataChart } from './MarketDataChart';
import { useWebSocketData } from '../hooks/useWebSocketData';
import { WebSocketConfig } from '../services/WebSocketService';
import { createLogger } from '../utils/logger';

const logger = createLogger('WebSocketTestChart');

interface TickData {
  time: number;
  price: number;
  volume?: number;
  symbol?: string;
}

interface DebugInfo {
  ticksReceived: number;
  candlesFormed: number;
  lastTick?: TickData;
  currentCandle?: ChartData;
  rawMessages: string[];
}

export interface WebSocketTestChartProps {
  symbol: string;
  timeframe: string;
  wsConfig: WebSocketConfig;
  showDebugPanel?: boolean;
  visible?: boolean;
}

export const WebSocketTestChart: React.FC<WebSocketTestChartProps> = ({
  symbol,
  timeframe,
  wsConfig,
  showDebugPanel = true,
  visible = true
}) => {
  const [debugInfo, setDebugInfo] = useState<DebugInfo>({
    ticksReceived: 0,
    candlesFormed: 0,
    rawMessages: []
  });
  
  // Chart references
  const seriesRef = useRef<any>(null);
  const lastCandleTimeRef = useRef<number>(0);
  
  // Candle builder state
  const currentCandleRef = useRef<ChartData | null>(null);
  const candlePeriodRef = useRef<number>(60); // Default 1 minute
  
  // Calculate candle period based on timeframe
  useEffect(() => {
    const periods: Record<string, number> = {
      '1m': 60,
      '5m': 300,
      '15m': 900,
      '30m': 1800,
      '1h': 3600,
      '4h': 14400,
      '12h': 43200,
      '1d': 86400
    };
    candlePeriodRef.current = periods[timeframe] || 60;
  }, [timeframe]);

  // Process tick into candle
  const processTick = (tick: TickData) => {
    const candleTime = Math.floor(tick.time / candlePeriodRef.current) * candlePeriodRef.current;
    
    logger.info('Processing tick', { 
      tickTime: new Date(tick.time * 1000).toISOString(),
      tickPrice: tick.price,
      candleTime: new Date(candleTime * 1000).toISOString(),
      currentCandleTime: currentCandleRef.current ? new Date(currentCandleRef.current.time * 1000).toISOString() : 'none',
      willCreateNewCandle: !currentCandleRef.current || currentCandleRef.current.time !== candleTime
    });
    
    if (!currentCandleRef.current || currentCandleRef.current.time !== candleTime) {
      // Complete previous candle if exists
      if (currentCandleRef.current) {
        const completedCandle = { ...currentCandleRef.current };
        logger.info('Candle completed', completedCandle);
        
        setDebugInfo(prev => ({
          ...prev,
          candlesFormed: prev.candlesFormed + 1
        }));
      }
      
      // Start new candle
      currentCandleRef.current = {
        time: Math.floor(candleTime) as any, // Ensure it's a number and treated as UTCTimestamp
        open: tick.price,
        high: tick.price,
        low: tick.price,
        close: tick.price
      };
    } else {
      // Update existing candle
      currentCandleRef.current.high = Math.max(currentCandleRef.current.high, tick.price);
      currentCandleRef.current.low = Math.min(currentCandleRef.current.low, tick.price);
      currentCandleRef.current.close = tick.price;
    }
    
    // Update debug info
    setDebugInfo(prev => ({
      ...prev,
      currentCandle: currentCandleRef.current ? { ...currentCandleRef.current } : undefined
    }));
    
    // Update chart with forming candle
    if (seriesRef.current && currentCandleRef.current) {
      try {
        // Only update if this candle time is >= the last one we updated
        if (currentCandleRef.current.time >= lastCandleTimeRef.current) {
          seriesRef.current.update(currentCandleRef.current);
          lastCandleTimeRef.current = currentCandleRef.current.time;
        } else {
          logger.warn('Skipping out-of-order candle update', {
            currentTime: currentCandleRef.current.time,
            lastTime: lastCandleTimeRef.current
          });
        }
      } catch (error) {
        logger.error('Failed to update chart', { 
          error, 
          candleTime: currentCandleRef.current.time,
          candleData: currentCandleRef.current 
        });
      }
    }
  };

  // WebSocket connection with custom message handler
  const { status, retryCount, connect, disconnect } = useWebSocketData({
    symbol,
    timeframe,
    wsConfig,
    enabled: visible,
    onUpdate: (candle) => {
      // This is called for processed candles
      logger.info('Received processed candle', candle);
    },
    onRawMessage: (message) => {
      // Handle raw WebSocket messages
      handleWebSocketMessage(message);
    }
  });

  // WebSocket message handler
  const handleWebSocketMessage = (data: any) => {
    try {
      // Log raw message
      logger.info('WebSocket data received', data);
      
      // Update debug info with raw message
      setDebugInfo(prev => ({
        ...prev,
        rawMessages: [...prev.rawMessages.slice(-9), JSON.stringify(data, null, 2)],
        ticksReceived: prev.ticksReceived + 1
      }));
      
      // Process based on data type
      if (data.type === 'tick' || (data.data && data.data.type === 'tick')) {
        const tickData = data.data || data;
        // Convert timestamp from ms to seconds
        let timestamp = tickData.timestamp || tickData.time || Date.now();
        if (timestamp > 9999999999) { // If timestamp is in milliseconds
          timestamp = timestamp / 1000;
        }
        
        const tick: TickData = {
          time: timestamp,
          price: tickData.price || tickData.data?.price,
          volume: tickData.volume || tickData.data?.volume
        };
        
        if (tick.price) {
          setDebugInfo(prev => ({
            ...prev,
            lastTick: tick
          }));
          
          processTick(tick);
        }
      }
    } catch (error) {
      logger.error('Error processing WebSocket data', error);
    }
  };

  return (
    <div style={{ display: 'flex', height: '100%', gap: '10px' }}>
      {/* Chart */}
      <div style={{ flex: showDebugPanel ? '1 1 60%' : '1 1 100%' }}>
        <MarketDataChart
          symbol={symbol}
          timeframe={timeframe}
        />
      </div>
      
      {/* Debug Panel */}
      {showDebugPanel && (
        <div style={{ 
          flex: '0 0 40%', 
          backgroundColor: '#1e1e1e',
          color: '#fff',
          padding: '20px',
          overflow: 'auto',
          fontFamily: 'monospace',
          fontSize: '12px'
        }}>
          <h3>WebSocket Debug Info</h3>
          
          <div style={{ marginBottom: '20px' }}>
            <h4>Connection</h4>
            <div>Status: <span style={{ color: status === 'connected' ? '#4caf50' : '#f44336' }}>{status}</span></div>
            <div>Retry Count: {retryCount}</div>
            <div>Symbol: {symbol}</div>
            <div>Timeframe: {timeframe}</div>
          </div>
          
          <div style={{ marginBottom: '20px' }}>
            <h4>Statistics</h4>
            <div>Ticks Received: {debugInfo.ticksReceived}</div>
            <div>Candles Formed: {debugInfo.candlesFormed}</div>
          </div>
          
          {debugInfo.lastTick && (
            <div style={{ marginBottom: '20px' }}>
              <h4>Last Tick</h4>
              <div>Time: {new Date(debugInfo.lastTick.time * 1000).toLocaleString()}</div>
              <div>Price: {debugInfo.lastTick.price}</div>
              <div>Volume: {debugInfo.lastTick.volume || 'N/A'}</div>
            </div>
          )}
          
          {debugInfo.currentCandle && (
            <div style={{ marginBottom: '20px' }}>
              <h4>Current Forming Candle</h4>
              <div>Time: {new Date(debugInfo.currentCandle.time * 1000).toLocaleString()}</div>
              <div>Open: {debugInfo.currentCandle.open}</div>
              <div>High: {debugInfo.currentCandle.high}</div>
              <div>Low: {debugInfo.currentCandle.low}</div>
              <div>Close: {debugInfo.currentCandle.close}</div>
            </div>
          )}
          
          <div style={{ marginBottom: '20px' }}>
            <h4>Actions</h4>
            <button onClick={connect} style={{ marginRight: '10px' }}>Reconnect</button>
            <button onClick={disconnect}>Disconnect</button>
          </div>
          
          <div>
            <h4>Raw Messages (Last 10)</h4>
            <div style={{ 
              backgroundColor: '#2d2d2d',
              padding: '10px',
              borderRadius: '4px',
              maxHeight: '400px',
              overflow: 'auto'
            }}>
              {debugInfo.rawMessages.map((msg, idx) => (
                <pre key={idx} style={{ 
                  borderBottom: '1px solid #444',
                  paddingBottom: '10px',
                  marginBottom: '10px'
                }}>
                  {msg}
                </pre>
              ))}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};