import { ISeriesPrimitivePaneView, SeriesPrimitivePaneViewZOrder } from 'lightweight-charts';
import type { ISeriesPrimitive, SeriesAttachedParameter } from 'lightweight-charts';
import { createLogger } from '../utils/logger';

const logger = createLogger('CountdownTimerPrimitive');

interface CountdownTimerOptions {
  timeframe: string;
  position?: 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right';
  offset?: { x: number; y: number };
  onNewCandleBoundary?: (time: number) => void;
}

class CountdownTimerPaneView implements ISeriesPrimitivePaneView {
  private _primitive: CountdownTimerPrimitive;

  constructor(primitive: CountdownTimerPrimitive) {
    this._primitive = primitive;
  }

  zOrder(): SeriesPrimitivePaneViewZOrder {
    return 'top';
  }

  renderer() {
    return {
      draw: (target: any) => {
        const ctx = target._context;
        if (!ctx) {
          return;
        }

        const { countdown, color, position, offset } = this._primitive.getData();

        ctx.save();

        // Set font
        ctx.font = '12px monospace';
        ctx.textBaseline = 'top';

        // Measure text for background box
        const countdownWidth = ctx.measureText(countdown).width;
        const totalWidth = countdownWidth + 16; // padding
        const height = 24;

        // Calculate position
        const canvasWidth = target._mediaSize.width;
        const canvasHeight = target._mediaSize.height;

        let x = 0;
        let y = 0;

        switch (position) {
          case 'top-left':
            x = offset.x;
            y = offset.y;
            break;
          case 'top-right':
            x = canvasWidth - totalWidth - offset.x;
            y = offset.y;
            break;
          case 'bottom-left':
            x = offset.x;
            y = canvasHeight - height - offset.y;
            break;
          case 'bottom-right':
            x = canvasWidth - totalWidth - offset.x;
            y = canvasHeight - height - offset.y;
            break;
        }

        // Draw background (more transparent)
        ctx.fillStyle = 'rgba(0, 0, 0, 0.3)';
        ctx.fillRect(x, y, totalWidth, height);

        // Draw countdown
        ctx.fillStyle = color;
        ctx.font = '12px monospace';
        ctx.fillText(countdown, x + 8, y + 6);

        ctx.restore();
      }
    };
  }
}

export class CountdownTimerPrimitive implements ISeriesPrimitive<any> {
  private _paneViews: CountdownTimerPaneView[];
  private _timeframe: string;
  private _position: 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right';
  private _offset: { x: number; y: number };
  private _onNewCandleBoundary?: (time: number) => void;
  private _countdown: string = '00:00';
  private _color: string = '#999';
  private _intervalId: number | null = null;
  private _requestUpdate: (() => void) | null = null;
  private _hasTriggered: boolean = false;

  constructor(options: CountdownTimerOptions) {
    this._timeframe = options.timeframe;
    this._position = options.position || 'bottom-right';
    this._offset = options.offset || { x: 10, y: 10 };
    this._onNewCandleBoundary = options.onNewCandleBoundary;
    this._paneViews = [new CountdownTimerPaneView(this)];
  }

  updateTimeframe(timeframe: string) {
    this._timeframe = timeframe;
    this._requestUpdate?.();
  }

  attached(param: SeriesAttachedParameter<any>): void {
    this._requestUpdate = param.requestUpdate;

    // Start countdown interval
    this._startCountdown();
  }

  detached(): void {
    this._stopCountdown();
  }

  paneViews() {
    return this._paneViews;
  }

  updateAllViews() {
    // Views are updated via requestUpdate callback
  }

  private _startCountdown() {
    // Initial update
    this._updateCountdown();

    // Update every second
    this._intervalId = window.setInterval(() => {
      this._updateCountdown();
    }, 1000);
  }

  private _stopCountdown() {
    if (this._intervalId !== null) {
      clearInterval(this._intervalId);
      this._intervalId = null;
    }
  }

  private _calculateSecondsToNextCandle(): number {
    const now = new Date();
    const seconds = now.getSeconds();
    const minutes = now.getMinutes();
    const hours = now.getHours();

    switch (this._timeframe) {
      case '5m':
        return (5 - (minutes % 5)) * 60 - seconds;
      case '15m':
        return (15 - (minutes % 15)) * 60 - seconds;
      case '1h':
        return (60 - minutes) * 60 - seconds;
      case '4h':
        return (4 - (hours % 4)) * 3600 + (60 - minutes) * 60 - seconds;
      case '12h':
        return (12 - (hours % 12)) * 3600 + (60 - minutes) * 60 - seconds;
      default:
        return (60 - minutes) * 60 - seconds;
    }
  }

  private _formatCountdown(totalSeconds: number): string {
    const displaySeconds = Math.max(0, totalSeconds);
    const minutes = Math.floor(displaySeconds / 60);
    const seconds = displaySeconds % 60;
    return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
  }

  private _getCountdownColor(seconds: number): string {
    if (seconds <= 10) {
      return '#ffae00'; // Yellow warning
    } else if (seconds <= 30) {
      return '#ccc'; // Brighter gray
    } else {
      return '#999'; // Dimmed gray
    }
  }

  private _updateCountdown() {
    const totalSeconds = this._calculateSecondsToNextCandle();
    this._countdown = this._formatCountdown(totalSeconds);
    this._color = this._getCountdownColor(totalSeconds);

    // Trigger boundary callback
    if (totalSeconds <= 0 && !this._hasTriggered) {
      this._hasTriggered = true;
      const candleTime = Math.floor(Date.now() / 1000);
      this._onNewCandleBoundary?.(candleTime);
      logger.debug('New candle boundary:', candleTime);
    }

    // Reset trigger when safely past boundary
    if (totalSeconds > 5) {
      this._hasTriggered = false;
    }

    // Request chart redraw
    this._requestUpdate?.();
  }

  getData() {
    return {
      countdown: this._countdown,
      color: this._color,
      position: this._position,
      offset: this._offset,
    };
  }
}