import { useEffect, useRef, useState } from 'react';
import { ChartData } from '../types';
import { WebSocketService, WebSocketConfig, getWebSocketService } from '../services/WebSocketService';
import { createLogger } from '../utils/logger';

const logger = createLogger('useWebSocketData');

export interface UseWebSocketDataProps {
  symbol: string;
  timeframe: string;
  wsConfig: WebSocketConfig;
  onUpdate?: (candle: ChartData) => void;
  onRawMessage?: (message: any) => void;
  enabled?: boolean;
}

export interface UseWebSocketDataReturn {
  status: 'disconnected' | 'connecting' | 'connected' | 'reconnecting' | 'failed';
  retryCount: number;
  lastUpdate?: ChartData;
  connect: () => Promise<void>;
  disconnect: () => void;
}

export function useWebSocketData({
  symbol,
  timeframe,
  wsConfig,
  onUpdate,
  onRawMessage,
  enabled = true
}: UseWebSocketDataProps): UseWebSocketDataReturn {
  const [status, setStatus] = useState<UseWebSocketDataReturn['status']>('disconnected');
  const [retryCount, setRetryCount] = useState(0);
  const [lastUpdate, setLastUpdate] = useState<ChartData>();
  const serviceRef = useRef<WebSocketService>();
  const unsubscribeRef = useRef<(() => void) | null>(null);

  useEffect(() => {
    if (!enabled) return;

    const initWebSocket = async () => {
      try {
        setStatus('connecting');

        // Use singleton pattern - get or create service
        serviceRef.current = getWebSocketService(wsConfig);

        // Only connect if not already connected
        if (serviceRef.current.getStatus() === 'disconnected') {
          await serviceRef.current.connect();
        }

        setStatus('connected');
        setRetryCount(0);

        // Subscribe to updates
        unsubscribeRef.current = serviceRef.current.subscribe(
          symbol,
          timeframe,
          (candle: ChartData) => {
            logger.trace('Received candle update', { symbol, timeframe, candle });

            setLastUpdate(candle);
            onUpdate?.(candle);
          },
          onRawMessage
        );
      } catch (error) {
        logger.error('Failed to connect WebSocket', { error });
        setStatus('failed');
      }
    };

    initWebSocket();

    // Monitor connection status
    const statusInterval = setInterval(() => {
      if (serviceRef.current) {
        const wsStatus = serviceRef.current.getStatus();
        const wsRetryCount = serviceRef.current.getRetryCount();

        setRetryCount(wsRetryCount);

        if (wsStatus === 'reconnecting') {
          setStatus('reconnecting');
        } else if (wsStatus === 'connected') {
          setStatus('connected');
        } else if (wsStatus === 'error' || wsStatus === 'disconnected') {
          setStatus('failed');
        }
      }
    }, 1000);

    return () => {
      clearInterval(statusInterval);

      // Unsubscribe from this specific subscription
      unsubscribeRef.current?.();

      // Disconnect the WebSocket when component unmounts
      if (serviceRef.current) {
        logger.info('Disconnecting WebSocket on component unmount');
        serviceRef.current.disconnect();
      }
    };
  }, [enabled, wsConfig.url, symbol, timeframe]);

  const connect = async () => {
    if (serviceRef.current && serviceRef.current.getStatus() === 'disconnected') {
      await serviceRef.current.connect();
    }
  };

  const disconnect = () => {
    serviceRef.current?.disconnect();
    setStatus('disconnected');
  };

  return {
    status,
    retryCount,
    lastUpdate,
    connect,
    disconnect
  };
}