/**
 * MARKET DATA CHART
 * Generic chart component for all market data (forex, bitcoin, crypto, etc.)
 * Based on the proven Bitcoin pattern with cascade aggregates
 *
 * Features:
 * - Fractal zoom with automatic timeframe switching
 * - Real-time data updates
 * - Works with any asset using the cascade pattern
 */

import React, { useEffect, useRef, useState } from 'react';
import { createChart, IChartApi, ISeriesApi, CandlestickData, Time } from 'lightweight-charts';
import { useChartStore } from '../stores/useChartStore';
import { Box } from '@mantine/core';
import { chartDataCoordinator, type SymbolMetadata } from '../services/ChartDataCoordinator';
import { CountdownTimerPrimitive } from '../primitives/CountdownTimerPrimitive';
import { TimeframeWatermarkPrimitive } from '../primitives/TimeframeWatermarkPrimitive';
import { usePlaceholderCandle, calculateCandleTime } from '../hooks/usePlaceholderCandle';
import { getDaysToShowForTimeframe, calculateBarSpacingForTimeframeSwitch } from '../utils/chartHelpers';
import { TIMEFRAME_SWITCH_THRESHOLDS } from '../constants/timeframes';
import { createLogger } from '../utils/logger';
import { ChartErrorBoundary } from './ErrorBoundary';
import { ChartSkeleton } from './LoadingState';
import { ChartEmptyState } from './EmptyState';

const logger = createLogger('MarketDataChart');

interface ChartData {
  time: number; // Unix timestamp
  open: number;
  high: number;
  low: number;
  close: number;
}

interface MarketDataChartProps {
  symbol?: string;
  timeframe?: string;
  onTimeframeChange?: (timeframe: string) => void;
  isFullscreen?: boolean;
  onToggleFullscreen?: () => void;
  enableTimeframeAutoSwitch?: boolean;
}


const MarketDataChart: React.FC<MarketDataChartProps> = ({
  symbol,
  timeframe,
  onTimeframeChange,
  enableTimeframeAutoSwitch = true,
}) => {
  const chartContainerRef = useRef<HTMLDivElement>(null);
  const chartRef = useRef<IChartApi | null>(null);
  const seriesRef = useRef<ISeriesApi<'Candlestick'> | null>(null);
  const countdownTimerRef = useRef<CountdownTimerPrimitive | null>(null);
  const watermarkRef = useRef<TimeframeWatermarkPrimitive | null>(null);

  const [currentTimeframe, setCurrentTimeframe] = useState(timeframe || '1h');
  const currentTimeframeRef = useRef(timeframe || '1h');
  const symbolRef = useRef(symbol || 'EURUSD');
  const [isLoading, setIsLoading] = useState(false);
  const [_error, setError] = useState<Error | null>(null);
  const isTransitioningRef = useRef(false);


  // Zustand store
  const {
    setCurrentTimeframe: setStoreTimeframe,
  } = useChartStore();

  // Placeholder candle management
  const {
    createPlaceholder,
    updateWithRealData,
    hasPlaceholder,
    resetTrigger,
  } = usePlaceholderCandle(seriesRef.current);

  // Transition cooldown tracking
  const lastTransitionRef = useRef<number>(0);
  const TRANSITION_COOLDOWN = 700; // Increased to match longer animation

  // Left edge locking
  const [isShiftPressed, setIsShiftPressed] = useState(false);
  const lockedLeftEdgeRef = useRef<number | null>(null);

  // Interval tracking
  const checkIntervalRef = useRef<ReturnType<typeof setInterval> | null>(null);


  // Track if initial load has been done
  const initialLoadDoneRef = useRef(false);

  // Format prices with appropriate precision based on magnitude
  const formatPrice = (price: number): string => {
    if (price >= 1000) {
      // Large prices (BTC, ETH) - 2 decimals
      return `$${price.toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`;
    } else if (price >= 100) {
      // Medium prices (SOL) - 2 decimals
      return `$${price.toFixed(2)}`;
    } else {
      // Small prices (forex) - 5 decimals for precision
      return `$${price.toFixed(5)}`;
    }
  };




  const fetchChartData = async (
    sym: string,
    tf: string,
    from?: number,
    to?: number
  ): Promise<{ data: ChartData[]; metadata: SymbolMetadata | null }> => {
    try {
      // Use coordinator for all data fetches
      const data = await chartDataCoordinator.fetchChartData(sym, tf, {
        range: from && to ? { from, to } : undefined
      });

      // Don't update dateRangeRef here - it should only be set during initial load

      // Get metadata separately if needed
      const metadata = await chartDataCoordinator.getSymbolMetadata(sym);

      return {
        data,
        metadata
      };
    } catch (error) {
      logger.error(`Error fetching market chart data for ${sym} ${tf}:`, error);
      return { data: [], metadata: null };
    }
  };

  // Rest of the component logic remains the same as AdaptiveChart
  // Using generic market data fetching and formatting...

  const switchTimeframe = async (newTimeframe: string) => {
    if (newTimeframe === currentTimeframeRef.current || isTransitioningRef.current) return;

    logger.debug(
      'Timeframe transition:',
      currentTimeframeRef.current,
      'â†’',
      newTimeframe
    );

    // Check cooldown
    const now = Date.now();
    if (now - lastTransitionRef.current < TRANSITION_COOLDOWN) {
      logger.debug('[COOLDOWN] Too fast! Wait a bit...');
      return;
    }

    lastTransitionRef.current = now;
    isTransitioningRef.current = true;

    // Store current view before switching
    const timeScale = chartRef.current!.timeScale();
    const visibleRange = timeScale.getVisibleRange();
    const currentBarSpacing = timeScale.options().barSpacing;
    const previousTimeframe = currentTimeframeRef.current;

    logger.info(
      `Executing transition: ${previousTimeframe} â†’ ${newTimeframe} at bar spacing ${currentBarSpacing}`
    );

    // Update ref immediately to prevent stale closures
    currentTimeframeRef.current = newTimeframe;

    // Update state
    setCurrentTimeframe(newTimeframe);
    setStoreTimeframe(newTimeframe);
    if (onTimeframeChange) {
      onTimeframeChange(newTimeframe);
    }

    // Update canvas primitives with new timeframe
    watermarkRef.current?.updateTimeframe(newTimeframe);
    countdownTimerRef.current?.updateTimeframe(newTimeframe);

    // Skip fade effect to prevent flashing
    // setChartOpacity(0.2);
    // await new Promise((resolve) => setTimeout(resolve, 300));

    try {
      // Let coordinator use its default range for this timeframe
      logger.debug(`Starting fetch for ${symbolRef.current} ${newTimeframe}`);

      const { data } = await fetchChartData(symbolRef.current!, newTimeframe);
      
      logger.debug(`Fetch completed, got ${data.length} candles`);

      if (data.length === 0) {
        logger.error(`No data available for ${newTimeframe} - aborting transition`);
        // Revert the transition
        currentTimeframeRef.current = previousTimeframe;
        setCurrentTimeframe(previousTimeframe);
        setStoreTimeframe(previousTimeframe);
        // Fade back in
        // setChartOpacity(1);
        isTransitioningRef.current = false;
        return;
      }

      if (data.length > 0 && seriesRef.current && chartRef.current) {
        // Calculate new bar spacing using the helper function
        const newBarSpacing = calculateBarSpacingForTimeframeSwitch(
          currentBarSpacing,
          previousTimeframe,
          newTimeframe
        );

        // Apply bar spacing before setting data
        chartRef.current.timeScale().applyOptions({
          barSpacing: newBarSpacing,
        });

        // Update data - use setData directly for timeframe switches
        logger.debug(`Setting ${data.length} candles on series`);
        
        // Apply the new data
        seriesRef.current.setData(data.map(d => ({ ...d, time: d.time as Time })));
        
        logger.debug(`Data set complete`);
        logger.debug(`Updated currentTimeframeRef.current to: ${currentTimeframeRef.current}`);

        // Maintain view range - MUST set the same time range
        if (visibleRange && chartRef.current) {
          // Force the chart to show the exact same time period
          logger.debug(`Restoring visible range: ${new Date((visibleRange.from as number) * 1000).toISOString()} to ${new Date((visibleRange.to as number) * 1000).toISOString()}`);
          
          // Apply the same visible range immediately
          chartRef.current.timeScale().setVisibleRange({
            from: visibleRange.from,
            to: visibleRange.to,
          });
          
          // Also ensure the bar spacing is maintained
          chartRef.current.timeScale().applyOptions({
            barSpacing: newBarSpacing,
          });
        }
      }

      // Fade back in
      // setChartOpacity(1);
      logger.info(`Transition complete to ${newTimeframe}`);
    } catch (error) {
      logger.error('Failed to switch timeframe:', error);
      // Revert everything on error
      currentTimeframeRef.current = previousTimeframe;
      setCurrentTimeframe(previousTimeframe);
      setStoreTimeframe(previousTimeframe);
      // setChartOpacity(1);
    } finally {
      logger.debug(`Setting isTransitioningRef to false`);
      isTransitioningRef.current = false;
    }
  };

  useEffect(() => {
    if (!chartContainerRef.current) return;

    logger.info('Creating chart');

    // Declare variables that need to be accessible in cleanup
    let crosshairUnsubscribe: (() => void) | undefined;

    // Create chart - matching AdaptiveChart exactly
    const chart = createChart(chartContainerRef.current, {
      autoSize: true, // Automatically size the chart to fit container
      layout: {
        background: { color: '#16161d' },
        textColor: '#d0d0d2',
      },
      grid: {
        vertLines: { color: '#1a2a3a' },
        horzLines: { color: '#1a2a3a' },
      },
      crosshair: {
        mode: 0, // Normal mode - shows both crosshair lines
        vertLine: {
          color: '#758696',
          width: 1,
          style: 3, // Dashed
          labelBackgroundColor: '#2B2B43',
        },
        horzLine: {
          color: '#758696',
          width: 1,
          style: 3, // Dashed
          labelBackgroundColor: '#2B2B43',
        },
      },
      timeScale: {
        visible: true, // Show the time axis
        timeVisible: true,
        secondsVisible: false,
        barSpacing: 12, // Default bar spacing
        minBarSpacing: 2, // Prevent excessive zoom out
        rightOffset: 0, // Set to 0 to work with fixRightEdge
        rightBarStaysOnScroll: true, // Keep the latest bar in view when scrolling
        fixLeftEdge: true, // Prevent panning before first candle
        fixRightEdge: true, // Prevent panning after last candle
        tickMarkFormatter: (time: number, tickMarkType: number) => {
          // Convert UTC timestamp to local time for axis labels
          const date = new Date(time * 1000);

          // Format based on the tick mark type
          if (tickMarkType === 0) {
            // Year
            return date.getFullYear().toString();
          } else if (tickMarkType === 1) {
            // Month
            const months = [
              'Jan',
              'Feb',
              'Mar',
              'Apr',
              'May',
              'Jun',
              'Jul',
              'Aug',
              'Sep',
              'Oct',
              'Nov',
              'Dec',
            ];
            return months[date.getMonth()];
          } else if (tickMarkType === 2) {
            // DayOfMonth with day of week
            const days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
            const dayName = days[date.getDay()];
            return `${dayName} ${date.getDate()}`;
          } else if (tickMarkType === 3) {
            // Time
            const hours = date.getHours();
            const minutes = date.getMinutes().toString().padStart(2, '0');
            const ampm = hours >= 12 ? 'PM' : 'AM';
            const displayHours = hours % 12 || 12; // Convert 0 to 12
            return `${displayHours}:${minutes} ${ampm}`;
          } else if (tickMarkType === 4) {
            // TimeWithSeconds
            const hours = date.getHours();
            const minutes = date.getMinutes().toString().padStart(2, '0');
            const seconds = date.getSeconds().toString().padStart(2, '0');
            const ampm = hours >= 12 ? 'PM' : 'AM';
            const displayHours = hours % 12 || 12; // Convert 0 to 12
            return `${displayHours}:${minutes}:${seconds} ${ampm}`;
          }

          // Default fallback
          return date.toLocaleString();
        },
      },
      localization: {
        timeFormatter: (timestamp: number) => {
          // Convert UTC timestamp to local time (12-hour format)
          const date = new Date(timestamp * 1000);
          const hours = date.getHours();
          const minutes = date.getMinutes().toString().padStart(2, '0');
          const ampm = hours >= 12 ? 'PM' : 'AM';
          const displayHours = hours % 12 || 12; // Convert 0 to 12
          return `${displayHours}:${minutes} ${ampm}`;
        },
      },
    });

    const candlestickSeries = chart.addCandlestickSeries({
      upColor: '#00ff88',
      downColor: '#ff4976',
      borderVisible: false,
      wickUpColor: '#00ff88',
      wickDownColor: '#ff4976',
      priceFormat: {
        type: 'custom',
        formatter: formatPrice,
      },
    });

    chartRef.current = chart;
    seriesRef.current = candlestickSeries;

    // Create canvas-based primitives
    watermarkRef.current = new TimeframeWatermarkPrimitive({
      timeframe: currentTimeframe,
      opacity: 0.2,
      fontSize: 76,
    });
    candlestickSeries.attachPrimitive(watermarkRef.current);

    countdownTimerRef.current = new CountdownTimerPrimitive({
      timeframe: currentTimeframe,
      position: 'bottom-right',
      offset: { x: 10, y: 10 },
      onNewCandleBoundary: (time: number) => {
        const candleTime = calculateCandleTime(time, currentTimeframe);
        createPlaceholder(candleTime);
      },
    });
    candlestickSeries.attachPrimitive(countdownTimerRef.current);

    // Create crosshair tooltip
    const toolTip = document.createElement('div');
    toolTip.style.cssText = `
      position: absolute;
      display: none;
      padding: 8px;
      box-sizing: border-box;
      font-size: 12px;
      text-align: left;
      z-index: 1000;
      top: 12px;
      left: 12px;
      pointer-events: none;
      background: rgba(0, 0, 0, 0.8);
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 4px;
      color: white;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    `;
    chartContainerRef.current.appendChild(toolTip);

    // Subscribe to crosshair move
    const unsubscribe = chart.subscribeCrosshairMove((param) => {
      if (!param.time || !param.seriesData.has(candlestickSeries)) {
        toolTip.style.display = 'none';
        return;
      }

      const data = param.seriesData.get(candlestickSeries) as CandlestickData;
      const timestamp =
        typeof param.time === 'string'
          ? parseInt(param.time) * 1000
          : (param.time as number) * 1000;
      const date = new Date(timestamp);

      // Format date and time
      const dateStr = date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
      });
      const timeStr = date.toLocaleTimeString('en-US', {
        hour: '2-digit',
        minute: '2-digit',
        hour12: true,
      });

      toolTip.style.display = 'block';
      toolTip.innerHTML = `
        <div style="color: #999; margin-bottom: 4px">${dateStr} ${timeStr}</div>
        <div style="color: #fff">O: ${formatPrice(data.open)}</div>
        <div style="color: #fff">H: ${formatPrice(data.high)}</div>
        <div style="color: #fff">L: ${formatPrice(data.low)}</div>
        <div style="color: ${data.close >= data.open ? '#00ff88' : '#ff4976'}">C: ${formatPrice(data.close)}</div>
      `;
    });

    // Store the unsubscribe function
    if (typeof unsubscribe === 'function') {
      crosshairUnsubscribe = unsubscribe;
    }

    // autoSize handles resizing automatically, no manual resize needed

    // CRITICAL: Monitor BAR SPACING changes instead of pixel widths
    let lastBarSpacing = 13;

    // Define checkTimeframeSwitch inside useEffect to access current state/refs
    const checkTimeframeSwitch = (barSpacing: number) => {
      logger.debug(`checkTimeframeSwitch called with barSpacing: ${barSpacing}`);
      logger.debug(`isTransitioningRef.current: ${isTransitioningRef.current}`);
      logger.debug(`currentTimeframeRef.current: ${currentTimeframeRef.current}`);

      // Check if auto-switching is disabled
      if (!enableTimeframeAutoSwitch) {
        logger.debug('[SWITCH] Auto-switching disabled');
        return;
      }

      if (isTransitioningRef.current) {
        logger.debug('[SWITCH] Skipping - transition in progress');
        return; // Silent skip during transitions
      }

      const currentTf = currentTimeframeRef.current;

      // Enforce minimum bar spacing for 12h to prevent excessive zoom out
      if (currentTf === '12h' && barSpacing < 3) {
        logger.debug('[ZOOM LIMIT] Enforcing minimum bar spacing for 12h');
        chartRef.current?.timeScale().applyOptions({
          barSpacing: 3,
        });
        return;
      }

      // 12h â†’ 4h (zooming in)
      if (currentTf === '12h' && barSpacing > TIMEFRAME_SWITCH_THRESHOLDS['12h'].zoomIn) {
        logger.info(
          `[SWITCH] 12h bar spacing ${barSpacing} > ${TIMEFRAME_SWITCH_THRESHOLDS['12h'].zoomIn} â†’ switching to 4h`
        );
        switchTimeframe('4h');
      }
      // 4h â†’ 12h (zooming out)
      else if (currentTf === '4h' && barSpacing < TIMEFRAME_SWITCH_THRESHOLDS['4h'].zoomOut) {
        logger.info(
          `[SWITCH] 4h bar spacing ${barSpacing} < ${TIMEFRAME_SWITCH_THRESHOLDS['4h'].zoomOut} â†’ switching to 12h`
        );
        switchTimeframe('12h');
      }
      // 4h â†’ 1h (zooming in)
      else if (currentTf === '4h' && barSpacing > TIMEFRAME_SWITCH_THRESHOLDS['4h'].zoomIn) {
        logger.info(
          `[SWITCH] 4h bar spacing ${barSpacing} > ${TIMEFRAME_SWITCH_THRESHOLDS['4h'].zoomIn} â†’ switching to 1h`
        );
        switchTimeframe('1h');
      }
      // 1h â†’ 4h (zooming out)
      else if (currentTf === '1h' && barSpacing < TIMEFRAME_SWITCH_THRESHOLDS['1h'].zoomOut) {
        logger.info(
          `[SWITCH] 1h bar spacing ${barSpacing} < ${TIMEFRAME_SWITCH_THRESHOLDS['1h'].zoomOut} â†’ switching to 4h`
        );
        switchTimeframe('4h');
      }
      // 1h â†’ 15m (zooming in)
      else if (currentTf === '1h' && barSpacing > TIMEFRAME_SWITCH_THRESHOLDS['1h'].zoomIn) {
        logger.info(
          `[SWITCH] 1h bar spacing ${barSpacing} > ${TIMEFRAME_SWITCH_THRESHOLDS['1h'].zoomIn} â†’ switching to 15m`
        );
        switchTimeframe('15m');
      }
      // 15m â†’ 1h (zooming out)
      else if (currentTf === '15m' && barSpacing < TIMEFRAME_SWITCH_THRESHOLDS['15m'].zoomOut) {
        logger.info(
          `[SWITCH] 15m bar spacing ${barSpacing} < ${TIMEFRAME_SWITCH_THRESHOLDS['15m'].zoomOut} â†’ switching to 1h`
        );
        switchTimeframe('1h');
      }
      // 15m â†’ 5m (zooming in)
      else if (currentTf === '15m' && barSpacing > TIMEFRAME_SWITCH_THRESHOLDS['15m'].zoomIn) {
        logger.info(
          `[SWITCH] 15m bar spacing ${barSpacing} > ${TIMEFRAME_SWITCH_THRESHOLDS['15m'].zoomIn} â†’ switching to 5m`
        );
        switchTimeframe('5m');
      }
      // 5m â†’ 15m (zooming out)
      else if (currentTf === '5m' && barSpacing < TIMEFRAME_SWITCH_THRESHOLDS['5m'].zoomOut) {
        logger.info(
          `[SWITCH] 5m bar spacing ${barSpacing} < ${TIMEFRAME_SWITCH_THRESHOLDS['5m'].zoomOut} â†’ switching to 15m`
        );
        switchTimeframe('15m');
      }
    };

    checkIntervalRef.current = setInterval(() => {
      if (!chartRef.current) {
        logger.debug('[ResolutionTracker] Chart not ready');
        return;
      }
      
      
      if (isTransitioningRef.current) {
        logger.debug('[ResolutionTracker] Skipping check - transition in progress');
        return;
      }

      try {
        const currentBarSpacing = chartRef.current.timeScale().options().barSpacing;

        if (currentBarSpacing !== lastBarSpacing && !isTransitioningRef.current) {
          logger.debug(
            `[ResolutionTracker] Current timeframe: ${currentTimeframeRef.current}, bar spacing: ${currentBarSpacing} (was: ${lastBarSpacing})`
          );
          logger.debug(`[ResolutionTracker] isTransitioningRef.current = ${isTransitioningRef.current}`);
          logger.debug(`[ResolutionTracker] About to call checkTimeframeSwitch`);
          lastBarSpacing = currentBarSpacing;
          checkTimeframeSwitch(currentBarSpacing);
        }
      } catch (e) {
        logger.error('[ResolutionTracker] Error in interval:', e);
        // Chart might be disposed, clear interval
        if (checkIntervalRef.current) {
          clearInterval(checkIntervalRef.current);
          checkIntervalRef.current = null;
        }
      }
    }, 100); // Check every 100ms

    // Handle Shift key for left edge locking
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Shift' && !isShiftPressed) {
        setIsShiftPressed(true);
        const timeScale = chartRef.current?.timeScale();
        if (timeScale) {
          const visibleRange = timeScale.getVisibleRange();
          if (visibleRange) {
            lockedLeftEdgeRef.current = visibleRange.from as number;
            logger.debug(
              '[LOCK LEFT] Activated, locking left edge at:',
              new Date((visibleRange.from as number) * 1000).toISOString()
            );
            // Disable rightBarStaysOnScroll temporarily
            timeScale.applyOptions({
              rightBarStaysOnScroll: false,
            });
          }
        }
      }
    };

    const handleKeyUp = (e: KeyboardEvent) => {
      if (e.key === 'Shift') {
        setIsShiftPressed(false);
        lockedLeftEdgeRef.current = null;
        logger.debug('[LOCK LEFT] Released, re-enabling right lock');
        // Re-enable rightBarStaysOnScroll
        chartRef.current?.timeScale().applyOptions({
          rightBarStaysOnScroll: true,
        });
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    window.addEventListener('keyup', handleKeyUp);

    return () => {
      window.removeEventListener('keydown', handleKeyDown);
      window.removeEventListener('keyup', handleKeyUp);
      if (checkIntervalRef.current) {
        clearInterval(checkIntervalRef.current);
      }
      // Unsubscribe from crosshair
      if (crosshairUnsubscribe && typeof crosshairUnsubscribe === 'function') {
        crosshairUnsubscribe();
      }
      // Remove tooltip
      if (toolTip && toolTip.parentNode) {
        toolTip.parentNode.removeChild(toolTip);
      }
      // Detach primitives before removing chart to prevent "Object is disposed" errors
      if (watermarkRef.current && candlestickSeries) {
        candlestickSeries.detachPrimitive(watermarkRef.current);
        watermarkRef.current = null;
      }
      if (countdownTimerRef.current && candlestickSeries) {
        candlestickSeries.detachPrimitive(countdownTimerRef.current);
        countdownTimerRef.current = null;
      }
      chart.remove();
    };
  }, []); // Only create chart once


  // Initial load effect
  useEffect(() => {
    if (initialLoadDoneRef.current) {
      logger.debug('Initial load already done, skipping');
      return;
    }

    let mounted = true;
    const loadInitialData = async () => {
      if (
        !mounted ||
        initialLoadDoneRef.current ||
        !seriesRef.current ||
        !chartRef.current ||
        !symbol
      )
        return;

      logger.info('Initial load triggered');
      initialLoadDoneRef.current = true;

      setIsLoading(true);
      setError(null);
      symbolRef.current = symbol;
      currentTimeframeRef.current = timeframe || '1h';

      try {
        // Fetch metadata to get dynamic date range
        const metadata = await chartDataCoordinator.getSymbolMetadata(symbol || 'EURUSD');

        // Use metadata to determine the end time, or fall back to current time
        const dataEndTime = metadata?.data_to || Math.floor(Date.now() / 1000);
        const dataStartTime = metadata?.data_from;

        const to = dataEndTime;

        // Use appropriate window based on timeframe
        let from;
        if (currentTimeframeRef.current === '5m') {
          from = to - 30 * 24 * 60 * 60; // Last 30 days of data
        } else {
          from = to - 90 * 24 * 60 * 60; // Last 90 days of data
        }

        // Ensure we don't go before data start time if available
        if (dataStartTime && from < dataStartTime) {
          from = dataStartTime;
        }

        // Set this as the default range for this symbol/timeframe combination
        chartDataCoordinator.setDefaultRange(
          symbol || 'EURUSD',
          currentTimeframeRef.current,
          from,
          to
        );

        const { data } = await fetchChartData(
          symbol || 'EURUSD',
          currentTimeframeRef.current,
          from,
          to
        );
        if (data.length > 0) {
          // For initial load, set data directly since placeholder hook isn't ready yet
          if (seriesRef.current && !hasPlaceholder()) {
            seriesRef.current.setData(data.map(d => ({ ...d, time: d.time as Time })));
          } else {
            updateWithRealData(data.map(d => ({ ...d, time: d.time as Time })));
          }
          // Generate cache key using the same from/to values we used for fetching

          // Set appropriate default view based on timeframe
          if (chartRef.current && data.length > 0) {
            const daysToShow = getDaysToShowForTimeframe(currentTimeframeRef.current);

            // Use the last data point's time as the end time instead of current time
            // This prevents issues with historical data
            const lastDataTime = data[data.length - 1]!.time;
            const from = lastDataTime - daysToShow * 24 * 60 * 60;

            chartRef.current.timeScale().setVisibleRange({
              from: from as Time,
              to: lastDataTime as Time,
            });

            logger.debug(`Set visible range to ${daysToShow} days ending at data time ${new Date(lastDataTime * 1000).toISOString()}`);
          }
        }
      } catch (error) {
        logger.error('Error loading market chart data:', error);
        setError(error instanceof Error ? error : new Error('Failed to load chart data'));
      } finally {
        setIsLoading(false);
      }
    };

    loadInitialData();
    return () => {
      mounted = false;
    };
  }, []); // Only run once on mount

  // Handle external timeframe changes
  useEffect(() => {
    if (
      timeframe &&
      timeframe !== currentTimeframeRef.current &&
      !isTransitioningRef.current &&
      initialLoadDoneRef.current
    ) {
      logger.info(`[EXTERNAL] Switching to ${timeframe} from external control`);
      switchTimeframe(timeframe);
    }
  }, [timeframe]);


  // Handle symbol prop changes
  useEffect(() => {
    if (!symbol) return;

    const prevSymbol = symbolRef.current;
    symbolRef.current = symbol;

    // Only reload if symbol actually changed AND initial load is done
    if (
      prevSymbol !== symbol &&
      chartRef.current &&
      initialLoadDoneRef.current &&
      prevSymbol !== undefined
    ) {
      logger.info('Symbol changed from', prevSymbol, 'to', symbol);

      // Clear existing chart data immediately
      if (seriesRef.current) {
        logger.debug('Clearing chart data for symbol change');
        seriesRef.current.setData([]);
      }

      // Reset symbol-specific state
      setIsLoading(true);
      setError(null);
      // Reset placeholder state via the hook
      if (resetTrigger) {
        resetTrigger();
      }

      // Reload data for new symbol using coordinator's default range
      fetchChartData(symbol, currentTimeframeRef.current)
        .then(({ data }) => {
          if (data.length > 0 && seriesRef.current) {
            // Direct setData for symbol change
            seriesRef.current.setData(data.map(d => ({ ...d, time: d.time as Time })));
            // Cache will be handled by the coordinator
          }
        })
        .catch((error) => {
          logger.error('Error loading new symbol:', error);
          setError(error instanceof Error ? error : new Error('Failed to load symbol data'));
        })
        .finally(() => setIsLoading(false));
    }
  }, [symbol]);





  // Show loading skeleton during initial load
  if (isLoading && !initialLoadDoneRef.current) {
    return (
      <ChartErrorBoundary>
        <Box style={{ position: 'relative', width: '100%', height: '100%' }}>
          <ChartSkeleton height="100%" />
        </Box>
      </ChartErrorBoundary>
    );
  }

  // Show empty state if no symbol
  if (!symbol) {
    return (
      <ChartErrorBoundary>
        <Box style={{ position: 'relative', width: '100%', height: '100%' }}>
          <ChartEmptyState />
        </Box>
      </ChartErrorBoundary>
    );
  }

  // Regular mode
  return (
    <ChartErrorBoundary onError={(err) => setError(err)}>
      <Box style={{ position: 'relative', width: '100%', height: '100%' }}>
          <div
            ref={chartContainerRef}
            style={{
              width: '100%',
              height: '100%',
              background: '#0a0a0a',
              position: 'relative',
              opacity: 1, // Always full opacity to prevent flashing
              // transition: 'opacity 300ms ease-in-out',
            }}
          >
          {isLoading && (
            <div
              style={{
                position: 'absolute',
                top: '10px',
                right: '10px',
                background: 'rgba(0,0,0,0.7)',
                color: '#fff',
                padding: '5px 10px',
                borderRadius: '4px',
                fontSize: '12px',
              }}
            >
              Loading...
            </div>
          )}

        <div
          style={{
            position: 'absolute',
            top: '10px',
            left: '10px',
            background: 'rgba(0,0,0,0.7)',
            color: '#00ff88',
            padding: '5px 10px',
            borderRadius: '4px',
            fontSize: '12px',
            fontFamily: 'monospace',
          }}
        >
          {currentTimeframe}
          {isShiftPressed && (
            <span style={{ marginLeft: '10px', color: '#ff9900' }}>[LOCK LEFT]</span>
          )}
        </div>
      </div>
    </Box>
    </ChartErrorBoundary>
  );
};

export { MarketDataChart };
export default MarketDataChart;
