import { createLogger } from '../utils/logger';
import { Candle, FetchCandlesArgs } from '../types';

const logger = createLogger('MockTauriApi');

// Cache for generated candles to simulate a real backend with caching
interface CacheEntry {
  candles: Candle[];
  timestamp: number;
}

const candleCache = new Map<string, CacheEntry>();
const CACHE_TTL = 5 * 60 * 1000; // 5 minutes (like a real API might cache)

// Metrics tracking
interface RequestMetrics {
  timestamp: number;
  symbol: string;
  timeframe: string;
  from: number;
  to: number;
  candlesReturned: number;
  cacheHit: boolean;
  latencyMs: number;
  bytesEstimate: number;
}

interface MetricsSummary {
  totalRequests: number;
  cacheHits: number;
  cacheMisses: number;
  cacheHitRate: number;
  totalCandles: number;
  totalBytesEstimate: number;
  averageLatencyMs: number;
  byTimeframe: Record<string, {
    requests: number;
    candles: number;
    avgCandlesPerRequest: number;
    bytesEstimate: number;
  }>;
  bySymbol: Record<string, {
    requests: number;
    candles: number;
    bytesEstimate: number;
  }>;
  requestLog: RequestMetrics[];
}

const requestMetrics: RequestMetrics[] = [];

// Network latency simulation
const LATENCY_ENABLED = false; // Set to false to disable for testing
const LATENCY_MIN = 50;  // Minimum latency in ms
const LATENCY_MAX = 150; // Maximum latency in ms

// Simulate network delay with randomness (like real networks)
const simulateNetworkLatency = async (): Promise<number> => {
  if (!LATENCY_ENABLED) return 0;

  const delay = LATENCY_MIN + Math.random() * (LATENCY_MAX - LATENCY_MIN);
  logger.info(`[NETWORK] Simulating ${Math.round(delay)}ms latency`);
  await new Promise(resolve => setTimeout(resolve, delay));
  return delay;
};

// Estimate bytes for a candle (rough approximation)
const estimateCandleBytes = (_candle: Candle): number => {
  // Each number is 8 bytes (float64), plus overhead for JSON structure
  // time, open, high, low, close, volume = 6 numbers * 8 bytes = 48 bytes
  // Add ~20 bytes for JSON overhead (commas, brackets, property names)
  return 68;
};

// Record a request in metrics
const recordRequest = (
  symbol: string,
  timeframe: string,
  from: number,
  to: number,
  candles: Candle[],
  cacheHit: boolean,
  latencyMs: number
): void => {
  const bytesEstimate = candles.length * estimateCandleBytes(candles[0] || {
    time: 0,
    open: 0,
    high: 0,
    low: 0,
    close: 0,
    volume: 0
  });

  const metric: RequestMetrics = {
    timestamp: Date.now(),
    symbol,
    timeframe,
    from,
    to,
    candlesReturned: candles.length,
    cacheHit,
    latencyMs,
    bytesEstimate
  };

  requestMetrics.push(metric);

  // Log detailed request info
  logger.info(
    `[METRICS] ${cacheHit ? 'CACHE HIT' : 'CACHE MISS'} | ` +
    `${symbol} ${timeframe} | ` +
    `${candles.length} candles | ` +
    `~${(bytesEstimate / 1024).toFixed(2)} KB | ` +
    `${latencyMs.toFixed(0)}ms`
  );
};

// Generate metrics summary
export const getMetricsSummary = (): MetricsSummary => {
  if (requestMetrics.length === 0) {
    return {
      totalRequests: 0,
      cacheHits: 0,
      cacheMisses: 0,
      cacheHitRate: 0,
      totalCandles: 0,
      totalBytesEstimate: 0,
      averageLatencyMs: 0,
      byTimeframe: {},
      bySymbol: {},
      requestLog: []
    };
  }

  const cacheHits = requestMetrics.filter(m => m.cacheHit).length;
  const cacheMisses = requestMetrics.length - cacheHits;
  const totalCandles = requestMetrics.reduce((sum, m) => sum + m.candlesReturned, 0);
  const totalBytes = requestMetrics.reduce((sum, m) => sum + m.bytesEstimate, 0);
  const avgLatency = requestMetrics.reduce((sum, m) => sum + m.latencyMs, 0) / requestMetrics.length;

  // Group by timeframe
  const byTimeframe: MetricsSummary['byTimeframe'] = {};
  requestMetrics.forEach(m => {
    if (!byTimeframe[m.timeframe]) {
      byTimeframe[m.timeframe] = { requests: 0, candles: 0, avgCandlesPerRequest: 0, bytesEstimate: 0 };
    }
    const tf = byTimeframe[m.timeframe];
    if (tf) {
      tf.requests++;
      tf.candles += m.candlesReturned;
      tf.bytesEstimate += m.bytesEstimate;
    }
  });

  // Calculate averages for timeframes
  Object.keys(byTimeframe).forEach(tf => {
    const stats = byTimeframe[tf];
    if (stats) {
      stats.avgCandlesPerRequest = stats.candles / stats.requests;
    }
  });

  // Group by symbol
  const bySymbol: MetricsSummary['bySymbol'] = {};
  requestMetrics.forEach(m => {
    if (!bySymbol[m.symbol]) {
      bySymbol[m.symbol] = { requests: 0, candles: 0, bytesEstimate: 0 };
    }
    const sym = bySymbol[m.symbol];
    if (sym) {
      sym.requests++;
      sym.candles += m.candlesReturned;
      sym.bytesEstimate += m.bytesEstimate;
    }
  });

  return {
    totalRequests: requestMetrics.length,
    cacheHits,
    cacheMisses,
    cacheHitRate: (cacheHits / requestMetrics.length) * 100,
    totalCandles,
    totalBytesEstimate: totalBytes,
    averageLatencyMs: avgLatency,
    byTimeframe,
    bySymbol,
    requestLog: [...requestMetrics] // Return a copy
  };
};

// Print formatted metrics summary to console
export const printMetricsSummary = (): void => {
  const summary = getMetricsSummary();

  console.log('\n' + '='.repeat(80));
  console.log('ðŸ“Š MOCK API METRICS SUMMARY');
  console.log('='.repeat(80));

  console.log('\nðŸ“ˆ OVERALL STATISTICS:');
  console.log(`  Total Requests:        ${summary.totalRequests}`);
  console.log(`  Cache Hits:            ${summary.cacheHits} (${summary.cacheHitRate.toFixed(1)}%)`);
  console.log(`  Cache Misses:          ${summary.cacheMisses}`);
  console.log(`  Total Candles:         ${summary.totalCandles.toLocaleString()}`);
  console.log(`  Total Data Volume:     ~${(summary.totalBytesEstimate / 1024).toFixed(2)} KB`);
  console.log(`  Average Latency:       ${summary.averageLatencyMs.toFixed(0)}ms`);

  console.log('\nðŸ“Š BY TIMEFRAME:');
  Object.entries(summary.byTimeframe)
    .sort(([, a], [, b]) => b.requests - a.requests)
    .forEach(([timeframe, stats]) => {
      console.log(`  ${timeframe.padEnd(6)} - ${stats.requests} requests | ` +
        `${stats.candles.toLocaleString()} candles | ` +
        `avg ${Math.round(stats.avgCandlesPerRequest)} candles/req | ` +
        `~${(stats.bytesEstimate / 1024).toFixed(2)} KB`);
    });

  console.log('\nðŸŽ¯ BY SYMBOL:');
  Object.entries(summary.bySymbol)
    .sort(([, a], [, b]) => b.requests - a.requests)
    .forEach(([symbol, stats]) => {
      console.log(`  ${symbol.padEnd(10)} - ${stats.requests} requests | ` +
        `${stats.candles.toLocaleString()} candles | ` +
        `~${(stats.bytesEstimate / 1024).toFixed(2)} KB`);
    });

  console.log('\n' + '='.repeat(80) + '\n');
};

// Reset metrics (useful for testing different scenarios)
export const resetMetrics = (): void => {
  requestMetrics.length = 0;
  logger.info('[METRICS] Reset all metrics');
};

// Helper to get base price for a symbol
const getBasePrice = (symbol: string): number => {
  if (symbol.includes('BTC')) return 112700;
  if (symbol.includes('ETH')) return 3800;
  if (symbol.includes('SOL')) return 220;
  return 1.0800;
};

// Helper to get volatility for a symbol
const getVolatility = (symbol: string): number => {
  return (symbol.includes('BTC') || symbol.includes('ETH') || symbol.includes('SOL')) ? 0.001 : 0.0001;
};

// Deterministic price generator with consistent seeding
const generateDeterministicPrice = (
  basePrice: number,
  volatility: number,
  daySeed: number,
  offset: number
): number => {
  // Multi-component price with consistent seed
  const trend = Math.sin(daySeed / 30) * basePrice * volatility * 2;
  const daily = Math.sin(daySeed) * basePrice * volatility;
  const micro = Math.sin((daySeed + offset) * 12.9898) * 43758.5453;
  const noise = (micro - Math.floor(micro) - 0.5) * basePrice * volatility * 0.5;

  return basePrice + trend + daily + noise;
};

// Generate cache key from request parameters
const getCacheKey = (symbol: string, timeframe: string, from: number, to: number): string => {
  return `${symbol}-${timeframe}-${from}-${to}`;
};

// Align time to candle boundary
const alignToCandleBoundary = (timestamp: number, interval: number): number => {
  // For 4h and 12h candles, align to UTC midnight
  if (interval === 14400) { // 4h in seconds
    // Align to 00:00, 04:00, 08:00, 12:00, 16:00, 20:00 UTC
    const date = new Date(timestamp * 1000);
    const hours = date.getUTCHours();
    const alignedHours = Math.floor(hours / 4) * 4;
    date.setUTCHours(alignedHours, 0, 0, 0);
    return Math.floor(date.getTime() / 1000);
  } else if (interval === 43200) { // 12h in seconds
    // Align to 00:00 and 12:00 UTC
    const date = new Date(timestamp * 1000);
    const hours = date.getUTCHours();
    const alignedHours = hours < 12 ? 0 : 12;
    date.setUTCHours(alignedHours, 0, 0, 0);
    return Math.floor(date.getTime() / 1000);
  }

  // For other intervals, use simple floor division
  return Math.floor(timestamp / interval) * interval;
};

// Generate candles hierarchically - always from 5m base for perfect alignment
const generateCandlesHierarchical = (
  from: number,
  to: number,
  symbol: string,
  timeframe: string
): Candle[] => {
  const intervals: Record<string, number> = {
    '5m': 300,
    '15m': 900,
    '1h': 3600,
    '4h': 14400,
    '12h': 43200
  };

  const targetInterval = intervals[timeframe] || 3600;
  const baseInterval = 300; // Always start with 5m base

  const basePrice = getBasePrice(symbol);
  const volatility = getVolatility(symbol);

  // Step 1: Generate ALL 5m candles for the range
  const base5mCandles: Candle[] = [];
  const alignedStart = alignToCandleBoundary(from, baseInterval);
  const alignedEnd = alignToCandleBoundary(to + targetInterval, baseInterval);

  for (let time = alignedStart; time < alignedEnd; time += baseInterval) {
    const seed = time / 86400;

    const open = generateDeterministicPrice(basePrice, volatility, seed, 0);
    const close = generateDeterministicPrice(basePrice, volatility, seed, 0.95);
    const high = generateDeterministicPrice(basePrice, volatility, seed, 0.3) * 1.0002;
    const low = generateDeterministicPrice(basePrice, volatility, seed, 0.6) * 0.9998;

    base5mCandles.push({
      time,
      open,
      high: Math.max(open, close, high),
      low: Math.min(open, close, low),
      close,
      volume: Math.floor(10000 + Math.sin(seed * 123.456) * 5000)
    });
  }

  // Step 2: If requesting 5m, return as-is
  if (targetInterval === baseInterval) {
    return base5mCandles.filter(c => c.time >= from && c.time <= to);
  }

  // Step 3: Aggregate 5m candles to target timeframe (OPTIMIZED - O(n) instead of O(nÂ²))
  const aggregated: Candle[] = [];
  const targetAlignedStart = alignToCandleBoundary(from, targetInterval);
  const targetAlignedEnd = alignToCandleBoundary(to + targetInterval, targetInterval);

  let baseIndex = 0; // Track position in base5mCandles array

  for (let time = targetAlignedStart; time < targetAlignedEnd; time += targetInterval) {
    // Skip candles before this window (sequential scan, not filter)
    while (baseIndex < base5mCandles.length) {
      const candle = base5mCandles[baseIndex];
      if (!candle || candle.time >= time) break;
      baseIndex++;
    }

    // Collect candles for this target window
    const subCandles: Candle[] = [];
    let idx = baseIndex;
    while (idx < base5mCandles.length) {
      const candle = base5mCandles[idx];
      if (!candle || candle.time >= time + targetInterval) break;
      subCandles.push(candle);
      idx++;
    }

    if (subCandles.length > 0) {
      const firstCandle = subCandles[0]!;
      const lastCandle = subCandles[subCandles.length - 1]!;

      aggregated.push({
        time,
        open: firstCandle.open,
        high: Math.max(...subCandles.map(c => c.high)),
        low: Math.min(...subCandles.map(c => c.low)),
        close: lastCandle.close,
        volume: subCandles.reduce((sum, c) => sum + (c.volume || 0), 0)
      });
    }
  }

  return aggregated.filter(c => c.time >= from && c.time <= to);
};

// Mock Tauri API for browser development
export const mockInvoke = async <T = unknown>(cmd: string, args?: Record<string, unknown>): Promise<T> => {
  logger.debug(`Command: ${cmd}`, args);

  // Mock responses for different commands
  switch (cmd) {
    case 'fetch_candles':
      // Return mock candle data
      if (!args || !('request' in args)) {
        throw new Error('Missing request parameter');
      }
      const { request } = args as unknown as FetchCandlesArgs;
      const { symbol, timeframe, from } = request;
      // Ensure we don't generate data beyond current time minus 2 minutes to leave room for real-time updates
      const now = Math.floor(Date.now() / 1000);
      const to = Math.min(request.to, now - 120);

      // Check cache first
      const cacheKey = getCacheKey(symbol, timeframe, from, to);
      const cached = candleCache.get(cacheKey);

      if (cached && Date.now() - cached.timestamp < CACHE_TTL) {
        logger.info(`[CACHE HIT] Returning ${cached.candles.length} cached ${timeframe} candles for ${symbol}`);
        // Even cache hits have some latency (network roundtrip)
        const latency = await simulateNetworkLatency();

        // Record metrics for cache hit
        recordRequest(symbol, timeframe, from, to, cached.candles, true, latency);

        return cached.candles as T;
      }

      // Cache miss - simulate network latency before generating
      logger.info(`[CACHE MISS] Generating ${timeframe} candles for ${symbol}`);
      const latency = await simulateNetworkLatency();

      const candles = generateCandlesHierarchical(from, to, symbol, timeframe);

      // Store in cache
      candleCache.set(cacheKey, {
        candles,
        timestamp: Date.now()
      });

      // Record metrics for cache miss
      recordRequest(symbol, timeframe, from, to, candles, false, latency);

      logger.info(`Generated ${candles.length} ${timeframe} candles (cached for 5min)`);
      return candles as T;

    case 'get_symbol_metadata':
      return {
        data_from: Date.now() / 1000 - (365 * 24 * 60 * 60), // 1 year ago
        data_to: Date.now() / 1000,
        total_ticks: 1000000
      } as T;

    case 'start_candle_monitor':
    case 'stop_candle_monitor':
      return null as T;

    default:
      logger.warn(`Unknown command: ${cmd}`);
      return null as T;
  }
};

export const mockListen = async <T = unknown>(event: string, handler: (payload: T) => void): Promise<() => void> => {
  logger.debug(`Listening to event: ${event}`);

  // Simulate some events for testing
  if (event.includes('candles-updated')) {
    // Simulate periodic candle updates
    const interval = setInterval(() => {
      const timeframe = event.split('-').pop(); // Extract timeframe from event name
      handler({
        symbol: 'EURUSD',
        timeframe: timeframe || '1h',
        timestamp: new Date().toISOString()
      } as T);
    }, 5000); // Every 5 seconds

    // Return unlisten function that clears the interval
    return () => {
      logger.debug(`Stopped listening to event: ${event}`);
      clearInterval(interval);
    };
  }

  // Return a mock unlisten function for other events
  return () => {
    console.log(`[Mock Tauri] Stopped listening to event: ${event}`);
  };
};
