import { invoke } from './tauriWrapper';
import { getHTTPDataProvider } from './HTTPDataProvider';
import { createLogger } from '../utils/logger';
import { Candle } from '../types';

// Auto-detect if we're in a browser environment (not Tauri)
const isBrowser = () => {
  if (typeof window === 'undefined') return false;
  try {
    // If no Tauri globals exist, we're in a browser
    return window.__TAURI__ === undefined &&
           (window as any).__TAURI_INTERNALS__ === undefined &&
           (window as any).__TAURI_IPC__ === undefined &&
           window.location.protocol !== 'tauri:';
  } catch {
    return false;
  }
};

interface ChartData {
  time: number;
  open: number;
  high: number;
  low: number;
  close: number;
}

interface CachedData {
  data: ChartData[];
  metadata: SymbolMetadata | null;
  timestamp: number;
  range: { from: number; to: number };
}

interface FetchOptions {
  forceRefresh?: boolean;
  range?: { from: number; to: number };
}

export interface SymbolMetadata {
  data_from: number;
  data_to: number;
  total_ticks?: number;
}

export class ChartDataCoordinator {
  private cache = new Map<string, CachedData>();
  private pendingRequests = new Map<string, Promise<ChartData[]>>();
  private defaultRanges = new Map<string, { from: number; to: number }>();
  private metadataCache = new Map<string, { metadata: SymbolMetadata; timestamp: number }>();
  private cacheTimeout = 10 * 60 * 1000; // 10 minutes
  private logger = createLogger('ChartDataCoordinator');
  private useHTTP = isBrowser(); // Auto-detect: true in browser, false in Tauri

  // Request tracking for debugging duplicate requests
  private requestStats = {
    totalRequests: 0,
    cacheHits: 0,
    cacheMisses: 0,
    pendingReuse: 0,
    backendCalls: 0
  };

  // Normalization factors must match backend
  private normalizationFactors: Record<string, number> = {
    '5m': 900,      // 15 minutes
    '15m': 3600,    // 1 hour
    '1h': 7200,     // 2 hours
    '4h': 14400,    // 4 hours
    '12h': 43200,   // 12 hours
  };

  /**
   * Get or set the default range for a symbol-timeframe combination
   */
  public setDefaultRange(symbol: string, timeframe: string, from: number, to: number) {
    const key = `${symbol}-${timeframe}`;
    this.defaultRanges.set(key, { from, to });
    this.logger.debug(`Set default range for ${key}: ${from} - ${to}`);
  }

  /**
   * Generate cache key matching backend logic
   */
  private getCacheKey(symbol: string, timeframe: string, from: number, to: number): string {
    const factor = this.normalizationFactors[timeframe] || 3600;
    const normalizedFrom = Math.floor(from / factor) * factor;
    const normalizedTo = Math.floor(to / factor) * factor;
    
    return `${symbol}-${timeframe}-${normalizedFrom}-${normalizedTo}`;
  }

  /**
   * Check if cached data is still valid
   */
  private isCacheValid(cached: CachedData): boolean {
    const age = Date.now() - cached.timestamp;
    return age < this.cacheTimeout;
  }

  /**
   * Calculate default range based on timeframe and available data
   */
  private async calculateDefaultRange(
    symbol: string,
    timeframe: string
  ): Promise<{ from: number; to: number }> {
    // Fetch metadata to get dynamic date range
    const metadata = await this.getSymbolMetadata(symbol);

    // If metadata is available, use it
    const dataEndTime = metadata?.data_to || Math.floor(Date.now() / 1000);
    const dataStartTime = metadata?.data_from || (dataEndTime - 365 * 24 * 60 * 60); // fallback: 1 year ago

    // Show last 30-90 days of available data
    const to = dataEndTime;
    let from: number;

    switch (timeframe) {
      case '1m':
      case '5m':
        from = to - 30 * 24 * 60 * 60; // Last 30 days of data
        break;
      case '15m':
        from = to - 30 * 24 * 60 * 60; // Last 30 days
        break;
      case '1h':
        from = to - 60 * 24 * 60 * 60; // Last 60 days
        break;
      default:
        from = dataStartTime; // Show all available data for 4h, 12h
    }

    // Ensure we don't request data before what's available
    if (from < dataStartTime) {
      from = dataStartTime;
    }

    this.logger.debug(
      `Calculated default range for ${symbol} ${timeframe}: ${new Date(from * 1000).toISOString()} to ${new Date(to * 1000).toISOString()}`
    );

    return { from, to };
  }

  /**
   * Main method to fetch chart data with coordination
   */
  public async fetchChartData(
    symbol: string, 
    timeframe: string, 
    options?: FetchOptions
  ): Promise<ChartData[]> {
    // Validate inputs
    if (!symbol || symbol.trim() === '') {
      this.logger.warn('Cannot fetch data for empty symbol');
      return [];
    }
    
    // 1. Determine the range to use
    let range: { from: number; to: number };
    
    if (options?.range) {
      // Use explicitly provided range
      range = options.range;
    } else {
      // Try to use stored default range for this symbol-timeframe
      const defaultKey = `${symbol}-${timeframe}`;
      const storedRange = this.defaultRanges.get(defaultKey);
      
      if (storedRange) {
        range = storedRange;
      } else {
        // Calculate and store default range
        range = await this.calculateDefaultRange(symbol, timeframe);
        this.setDefaultRange(symbol, timeframe, range.from, range.to);
      }
    }

    // Track request
    this.requestStats.totalRequests++;

    // 2. Generate cache key
    const cacheKey = this.getCacheKey(symbol, timeframe, range.from, range.to);
    this.logger.debug(`[REQUEST #${this.requestStats.totalRequests}] ${symbol}-${timeframe} â†’ cache key: ${cacheKey}`);
    this.logger.debug(`  Raw range: ${range.from} - ${range.to}`);
    this.logger.debug(`  Normalized: ${this.getCacheKey(symbol, timeframe, range.from, range.to)}`);

    // 3. Check for in-flight requests
    if (this.pendingRequests.has(cacheKey)) {
      this.requestStats.pendingReuse++;
      this.logger.info(`[PENDING REUSE] Reusing pending request for ${cacheKey} (${this.requestStats.pendingReuse} total reuses)`);
      return this.pendingRequests.get(cacheKey)!;
    }

    // 4. Check cache (unless force refresh)
    if (!options?.forceRefresh) {
      const cached = this.cache.get(cacheKey);
      if (cached && this.isCacheValid(cached)) {
        this.requestStats.cacheHits++;
        this.logger.info(`[CACHE HIT] ${cacheKey} (${cached.data.length} candles, ${this.requestStats.cacheHits} total hits)`);
        return cached.data;
      }
    }

    // 5. Create new request
    this.requestStats.cacheMisses++;
    this.requestStats.backendCalls++;
    this.logger.warn(`[CACHE MISS] ${cacheKey} â†’ Backend call #${this.requestStats.backendCalls}`);
    
    const requestPromise = this.doFetch(symbol, timeframe, range.from, range.to)
      .then(result => {
        // Cache the result with metadata
        this.cache.set(cacheKey, {
          data: result.data,
          metadata: result.metadata,
          timestamp: Date.now(),
          range
        });
        
        // Clean up pending request
        this.pendingRequests.delete(cacheKey);
        
        this.logger.info(`Fetched and cached ${result.data.length} candles for ${cacheKey}`);
        return result.data;
      })
      .catch(error => {
        // Clean up pending request on error
        this.pendingRequests.delete(cacheKey);
        throw error;
      });

    // Store as pending
    this.pendingRequests.set(cacheKey, requestPromise);
    
    return requestPromise;
  }

  /**
   * Enable HTTP data provider
   */
  public enableHTTP(enable: boolean = true) {
    this.useHTTP = enable;
    this.logger.info(`HTTP data provider ${enable ? 'enabled' : 'disabled'}`);
  }

  /**
   * Perform the actual backend fetch
   */
  private async doFetch(
    symbol: string,
    timeframe: string,
    from: number,
    to: number
  ): Promise<{ data: ChartData[]; metadata: SymbolMetadata | null }> {
    try {
      let response: any;

      // Use HTTP data provider if enabled
      if (this.useHTTP) {
        try {
          const httpProvider = getHTTPDataProvider();
          const candles = await httpProvider.fetchCandles({
            symbol,
            timeframe,
            from,
            to
          });
          response = candles;
        } catch (providerError) {
          this.logger.error(
            'HTTPDataProvider not initialized. Initialize it before using charts:\n' +
            'import { getHTTPDataProvider } from "sptrader-chart-lib";\n' +
            'getHTTPDataProvider({ baseUrl: "http://localhost:8080" });'
          );
          throw providerError;
        }
      } else {
        // Use mock/tauri invoke
        response = await invoke('fetch_candles', {
          request: {
            symbol,
            timeframe,
            from,
            to
          }
        });
      }

      if (!response || !Array.isArray(response)) {
        this.logger.error('Invalid response format:', response);
        return { data: [], metadata: null };
      }

      // The backend returns candles with time as Unix timestamp
      const data = response.map((candle: Candle) => ({
        time: candle.time, // Already Unix timestamp from backend
        open: candle.open,
        high: candle.high,
        low: candle.low,
        close: candle.close,
      }));

      // For now, we don't have metadata from this endpoint
      const metadata = null;

      return { data, metadata };
    } catch (error) {
      this.logger.error('Fetch error:', error);
      throw error;
    }
  }

  /**
   * Get metadata for a symbol
   */
  public async getSymbolMetadata(symbol: string): Promise<SymbolMetadata | null> {
    // Don't fetch metadata for empty symbols
    if (!symbol || symbol.trim() === '') {
      this.logger.warn('Skipping metadata fetch for empty symbol');
      return null;
    }

    // Check dedicated metadata cache first
    const cachedMeta = this.metadataCache.get(symbol);
    if (cachedMeta) {
      const age = Date.now() - cachedMeta.timestamp;
      if (age < this.cacheTimeout) {
        this.logger.debug(`Using cached metadata for ${symbol}`);
        return cachedMeta.metadata;
      }
    }

    // Check if we have cached metadata from recent candles fetch
    for (const [key, cached] of this.cache.entries()) {
      if (key.startsWith(`${symbol}-`) && this.isCacheValid(cached) && cached.metadata) {
        this.logger.trace('Using metadata from candles cache for', symbol);
        return cached.metadata;
      }
    }

    // If no cached metadata, make a separate request
    try {
      let metadata: SymbolMetadata;

      if (this.useHTTP) {
        // Use HTTP data provider
        try {
          const httpProvider = getHTTPDataProvider();
          const httpMetadata = await httpProvider.fetchMetadata(symbol);
          // Convert HTTP format to SymbolMetadata format
          metadata = {
            data_from: httpMetadata.earliest,
            data_to: httpMetadata.latest
          };
        } catch (providerError) {
          this.logger.error(
            'HTTPDataProvider not initialized. Initialize it before using charts:\n' +
            'import { getHTTPDataProvider, chartDataCoordinator } from "sptrader-chart-lib";\n' +
            'getHTTPDataProvider({ baseUrl: "http://localhost:8080" });\n' +
            'chartDataCoordinator.enableHTTP(true); // Optional if auto-detected'
          );
          throw providerError;
        }
      } else {
        // Use mock/tauri invoke
        metadata = await invoke<SymbolMetadata>('get_symbol_metadata', { symbol });
      }

      // Cache the metadata
      this.metadataCache.set(symbol, {
        metadata,
        timestamp: Date.now()
      });

      return metadata;
    } catch (error) {
      this.logger.error('Failed to get metadata:', error);
      return null;
    }
  }

  /**
   * Invalidate cache entries
   */
  public invalidateCache(pattern?: string) {
    if (!pattern) {
      this.logger.info('Clearing entire cache');
      this.cache.clear();
      this.metadataCache.clear();
      this.defaultRanges.clear();
      return;
    }

    let removed = 0;
    let metadataRemoved = 0;

    // Clear matching candle data cache entries
    for (const [key] of this.cache) {
      if (key.includes(pattern)) {
        this.cache.delete(key);
        removed++;
      }
    }

    // Clear matching metadata cache entries
    for (const [symbol] of this.metadataCache) {
      if (symbol.includes(pattern)) {
        this.metadataCache.delete(symbol);
        metadataRemoved++;
      }
    }

    // Clear matching default ranges
    for (const [key] of this.defaultRanges) {
      if (key.includes(pattern)) {
        this.defaultRanges.delete(key);
      }
    }

    this.logger.info(`Invalidated ${removed} cache entries and ${metadataRemoved} metadata entries matching: ${pattern}`);
  }

  /**
   * Cancel all pending requests
   */
  public cancelPendingRequests() {
    this.logger.info(`Cancelling ${this.pendingRequests.size} pending requests`);
    this.pendingRequests.clear();
  }

  /**
   * Get cache statistics
   */
  public getCacheStats() {
    return {
      cacheSize: this.cache.size,
      pendingRequests: this.pendingRequests.size,
      defaultRanges: this.defaultRanges.size,
      requestStats: this.requestStats
    };
  }

  /**
   * Get request statistics for debugging
   */
  public getRequestStats() {
    const hitRate = this.requestStats.totalRequests > 0
      ? ((this.requestStats.cacheHits / this.requestStats.totalRequests) * 100).toFixed(1)
      : '0';

    return {
      ...this.requestStats,
      cacheHitRate: `${hitRate}%`,
      duplicatesAvoided: this.requestStats.pendingReuse
    };
  }

  /**
   * Reset request statistics
   */
  public resetRequestStats() {
    this.requestStats = {
      totalRequests: 0,
      cacheHits: 0,
      cacheMisses: 0,
      pendingReuse: 0,
      backendCalls: 0
    };
    this.logger.info('Request statistics reset');
  }
}

// Export singleton instance
export const chartDataCoordinator = new ChartDataCoordinator();