import { mockInvoke, mockListen } from './mockTauriApi';
import { createLogger } from '../utils/logger';

const logger = createLogger('TauriWrapper');

// Check if we're running in Tauri
const isTauri = () => {
  try {
    // Check multiple ways to detect Tauri environment
    return window.__TAURI__ !== undefined ||
           (window as any).__TAURI_INTERNALS__ !== undefined ||
           (window as any).__TAURI_IPC__ !== undefined ||
           window.location.protocol === 'tauri:' ||
           window.location.protocol === 'https:';  // Tauri v2 uses https protocol
  } catch {
    return false;
  }
};

// Create invoke and listen functions that use the appropriate implementation
export const invoke = async <T = unknown>(cmd: string, args?: Record<string, unknown>): Promise<T> => {
  if (isTauri()) {
    // Dynamically import Tauri API only when needed
    const { invoke: tauriInvoke } = await import('@tauri-apps/api/core');
    return tauriInvoke<T>(cmd, args);
  } else {
    return mockInvoke<T>(cmd, args);
  }
};

export const listen = async <T = unknown>(event: string, handler: (event: { payload: T }) => void): Promise<() => void> => {
  if (isTauri()) {
    // Dynamically import Tauri API only when needed
    const { listen: tauriListen } = await import('@tauri-apps/api/event');
    return tauriListen<T>(event, handler);
  } else {
    // Mock listen needs to match the Tauri event structure
    return mockListen(event, (payload: T) => handler({ payload }));
  }
};

// Log the environment
if (isTauri()) {
  logger.info('Running in Tauri environment');
} else {
  logger.info('Running in browser - using mock API');
}