import { createLogger } from '../utils/logger';

const logger = createLogger('ReconnectionManager');

export interface ReconnectionConfig {
  maxRetries?: number;
  initialDelay?: number;
  maxDelay?: number;
  backoffMultiplier?: number;
  onStatusChange?: (status: ConnectionStatus) => void;
  onError?: (error: Error, retryCount: number) => void;
  onConnected?: () => void;
  onDisconnected?: (reason?: string) => void;
  onMaxRetriesReached?: () => void;
}

export type ConnectionStatus = 'connected' | 'connecting' | 'disconnected' | 'error' | 'reconnecting';

export class ReconnectionManager {
  private config: Required<ReconnectionConfig>;
  private retryCount = 0;
  private retryTimer?: NodeJS.Timeout;
  private currentDelay: number;
  private isDestroyed = false;
  private connectFn: () => Promise<void>;
  private currentStatus: ConnectionStatus = 'disconnected';

  constructor(
    connectFunction: () => Promise<void>,
    config: ReconnectionConfig = {}
  ) {
    this.connectFn = connectFunction;
    this.config = {
      maxRetries: config.maxRetries ?? 10,
      initialDelay: config.initialDelay ?? 1000,
      maxDelay: config.maxDelay ?? 30000,
      backoffMultiplier: config.backoffMultiplier ?? 1.5,
      onStatusChange: config.onStatusChange ?? (() => {}),
      onError: config.onError ?? (() => {}),
      onConnected: config.onConnected ?? (() => {}),
      onDisconnected: config.onDisconnected ?? (() => {}),
      onMaxRetriesReached: config.onMaxRetriesReached ?? (() => {}),
    };
    this.currentDelay = this.config.initialDelay;
  }

  private setStatus(status: ConnectionStatus) {
    if (this.currentStatus !== status) {
      this.currentStatus = status;
      logger.info(`Status changed: ${status}`);
      this.config.onStatusChange(status);
    }
  }

  async connect(): Promise<void> {
    if (this.isDestroyed) {
      logger.warn('Cannot connect: ReconnectionManager is destroyed');
      return;
    }

    this.clearRetryTimer();
    this.setStatus('connecting');

    try {
      await this.connectFn();
      this.handleConnectionSuccess();
    } catch (error) {
      this.handleConnectionError(error as Error);
    }
  }

  private handleConnectionSuccess() {
    logger.info('Connection successful');
    this.retryCount = 0;
    this.currentDelay = this.config.initialDelay;
    this.setStatus('connected');
    this.config.onConnected();
  }

  private handleConnectionError(error: Error) {
    logger.error('Connection failed:', error);
    this.config.onError(error, this.retryCount);
    
    if (this.retryCount >= this.config.maxRetries) {
      this.handleMaxRetriesReached();
      return;
    }

    this.scheduleRetry();
  }

  private handleMaxRetriesReached() {
    logger.error(`Max retries (${this.config.maxRetries}) reached`);
    this.setStatus('error');
    this.config.onMaxRetriesReached();
  }

  private scheduleRetry() {
    if (this.isDestroyed) return;

    this.retryCount++;
    this.setStatus('reconnecting');
    
    logger.info(`Scheduling retry ${this.retryCount}/${this.config.maxRetries} in ${this.currentDelay}ms`);

    this.retryTimer = setTimeout(() => {
      this.connect();
    }, this.currentDelay);

    // Calculate next delay with exponential backoff
    this.currentDelay = Math.min(
      this.currentDelay * this.config.backoffMultiplier,
      this.config.maxDelay
    );
  }

  disconnect(reason?: string) {
    logger.info('Disconnecting', reason ? `Reason: ${reason}` : '');
    this.clearRetryTimer();
    this.setStatus('disconnected');
    this.config.onDisconnected(reason);
  }

  private clearRetryTimer() {
    if (this.retryTimer) {
      clearTimeout(this.retryTimer);
      this.retryTimer = undefined;
    }
  }

  resetRetryCount() {
    this.retryCount = 0;
    this.currentDelay = this.config.initialDelay;
  }

  getStatus(): ConnectionStatus {
    return this.currentStatus;
  }

  getRetryCount(): number {
    return this.retryCount;
  }

  getNextRetryDelay(): number {
    return this.currentDelay;
  }

  destroy() {
    logger.info('Destroying ReconnectionManager');
    this.isDestroyed = true;
    this.clearRetryTimer();
    this.disconnect('Manager destroyed');
  }
}

// Helper class for WebSocket reconnection
export class WebSocketReconnectionManager extends ReconnectionManager {
  private ws?: WebSocket;
  private url: string;
  private protocols?: string | string[];
  private heartbeatInterval?: NodeJS.Timeout;
  private heartbeatTimeout?: NodeJS.Timeout;
  private lastPingTime?: number;
  private pingInterval = 30000; // 30 seconds
  private pongTimeout = 5000; // 5 seconds

  constructor(
    url: string,
    protocols?: string | string[],
    config?: ReconnectionConfig & {
      enableHeartbeat?: boolean;
      pingInterval?: number;
      pongTimeout?: number;
      onMessage?: (event: MessageEvent) => void;
    }
  ) {
    const wsConfig = { ...config };
    const onMessage = wsConfig.onMessage;
    delete wsConfig.onMessage;

    super(async () => {
      await this.createWebSocket(onMessage);
    }, wsConfig);

    this.url = url;
    this.protocols = protocols;
    
    if (config?.enableHeartbeat) {
      this.pingInterval = config.pingInterval ?? this.pingInterval;
      this.pongTimeout = config.pongTimeout ?? this.pongTimeout;
    } else {
      // Clear ping values when heartbeat is disabled
      this.pingInterval = undefined as any;
      this.pongTimeout = undefined as any;
    }
  }

  private async createWebSocket(onMessage?: (event: MessageEvent) => void): Promise<void> {
    return new Promise((resolve, reject) => {
      try {
        this.ws = new WebSocket(this.url, this.protocols);
        
        this.ws.onopen = () => {
          logger.info('WebSocket connected');
          this.startHeartbeat();
          resolve();
        };

        this.ws.onerror = (event) => {
          logger.error('WebSocket error:', event);
          reject(new Error('WebSocket connection failed'));
        };

        this.ws.onclose = (event) => {
          logger.info('WebSocket closed:', { code: event.code, reason: event.reason });
          this.stopHeartbeat();
          
          // Only reconnect if not a normal closure
          if (event.code !== 1000 && event.code !== 1001) {
            this.disconnect('WebSocket closed unexpectedly');
            this.connect(); // Trigger reconnection
          }
        };

        if (onMessage) {
          this.ws.onmessage = onMessage;
        }

        // Handle ping/pong for heartbeat
        this.ws.addEventListener('message', (event) => {
          if (event.data === 'pong') {
            this.handlePong();
          }
        });

      } catch (error) {
        reject(error);
      }
    });
  }

  private startHeartbeat() {
    if (!this.pingInterval) return;

    this.heartbeatInterval = setInterval(() => {
      this.sendPing();
    }, this.pingInterval);
  }

  private stopHeartbeat() {
    if (this.heartbeatInterval) {
      clearInterval(this.heartbeatInterval);
      this.heartbeatInterval = undefined;
    }
    if (this.heartbeatTimeout) {
      clearTimeout(this.heartbeatTimeout);
      this.heartbeatTimeout = undefined;
    }
  }

  private sendPing() {
    if (this.ws?.readyState === WebSocket.OPEN) {
      this.lastPingTime = Date.now();
      this.ws.send('ping');
      
      // Set timeout for pong response
      this.heartbeatTimeout = setTimeout(() => {
        logger.warn('Heartbeat timeout - no pong received');
        this.ws?.close();
      }, this.pongTimeout);
    }
  }

  private handlePong() {
    if (this.heartbeatTimeout) {
      clearTimeout(this.heartbeatTimeout);
      this.heartbeatTimeout = undefined;
    }
    
    if (this.lastPingTime) {
      const latency = Date.now() - this.lastPingTime;
      logger.trace(`Heartbeat latency: ${latency}ms`);
    }
  }

  send(data: string | ArrayBuffer | Blob): void {
    if (this.ws?.readyState === WebSocket.OPEN) {
      this.ws.send(data);
    } else {
      logger.warn('Cannot send: WebSocket is not open');
    }
  }

  close(code?: number, reason?: string): void {
    this.stopHeartbeat();
    this.ws?.close(code, reason);
    this.destroy();
  }

  getWebSocket(): WebSocket | undefined {
    return this.ws;
  }
}