import { Box } from '@mantine/core';
import MarketDataChart from '../components/MarketDataChart';
import { useTradingStore } from '../stores/useTradingStore';

interface MarketChartPageProps {
  symbol?: string;
  timeframe?: string;
  enableTimeframeAutoSwitch?: boolean;
}

export const MarketChartPage = ({
  symbol,
  timeframe,
  enableTimeframeAutoSwitch = true
}: MarketChartPageProps = {}) => {
  const { selectedPair, selectedTimeframe } = useTradingStore();

  // Use props if provided, otherwise fall back to store
  const chartSymbol = symbol || selectedPair;
  const chartTimeframe = timeframe || selectedTimeframe || '1h';

  return (
    <Box style={{ height: '100%', width: '100%', background: '#0a0a0a', position: 'relative' }}>
      <MarketDataChart
        symbol={chartSymbol}
        timeframe={chartTimeframe}
        enableTimeframeAutoSwitch={enableTimeframeAutoSwitch}
      />
    </Box>
  );
};
