import React from 'react';

interface EmptyStateProps {
  title?: string;
  message?: string;
  icon?: React.ReactNode;
  action?: {
    label: string;
    onClick: () => void;
  };
  height?: string | number;
}

export const EmptyState: React.FC<EmptyStateProps> = ({
  title = 'No Data Available',
  message = 'There is no data to display at this time.',
  icon,
  action,
  height = '100%',
}) => {
  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        height: typeof height === 'number' ? `${height}px` : height,
        minHeight: '300px',
        padding: '40px',
        textAlign: 'center',
      }}
    >
      {icon || (
        <div
          style={{
            fontSize: '48px',
            marginBottom: '20px',
            color: '#dee2e6',
          }}
        >
          ðŸ“Š
        </div>
      )}
      <h3
        style={{
          margin: '0 0 10px 0',
          fontSize: '20px',
          fontWeight: '600',
          color: '#495057',
        }}
      >
        {title}
      </h3>
      <p
        style={{
          margin: '0 0 20px 0',
          fontSize: '14px',
          color: '#6c757d',
          maxWidth: '400px',
        }}
      >
        {message}
      </p>
      {action && (
        <button
          onClick={action.onClick}
          style={{
            padding: '10px 20px',
            fontSize: '14px',
            fontWeight: '500',
            color: '#fff',
            backgroundColor: '#007bff',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer',
            transition: 'background-color 0.2s',
          }}
          onMouseEnter={(e) => {
            e.currentTarget.style.backgroundColor = '#0056b3';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.backgroundColor = '#007bff';
          }}
        >
          {action.label}
        </button>
      )}
    </div>
  );
};

// Chart-specific empty state
export const ChartEmptyState: React.FC<{
  symbol?: string;
  onSelectSymbol?: () => void;
}> = ({ symbol, onSelectSymbol }) => {
  if (!symbol) {
    return (
      <EmptyState
        title="No Symbol Selected"
        message="Please select a trading pair to view the chart."
        icon={
          <div style={{ fontSize: '48px', marginBottom: '20px', color: '#dee2e6' }}>
            ðŸ”
          </div>
        }
        action={
          onSelectSymbol
            ? {
                label: 'Select Symbol',
                onClick: onSelectSymbol,
              }
            : undefined
        }
        height="400px"
      />
    );
  }

  return (
    <EmptyState
      title="No Chart Data"
      message={`Unable to load chart data for ${symbol}. Please try again later.`}
      height="400px"
    />
  );
};

// No data in range state
export const NoDataInRangeState: React.FC<{
  timeframe: string;
  onChangeTimeframe?: () => void;
}> = ({ timeframe, onChangeTimeframe }) => {
  return (
    <EmptyState
      title="No Data in This Range"
      message={`There is no data available for the ${timeframe} timeframe in the current range.`}
      icon={
        <div style={{ fontSize: '48px', marginBottom: '20px', color: '#dee2e6' }}>
          ðŸ“…
        </div>
      }
      action={
        onChangeTimeframe
          ? {
              label: 'Change Timeframe',
              onClick: onChangeTimeframe,
            }
          : undefined
      }
      height="400px"
    />
  );
};